import React from 'react';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';


const useStyles = makeStyles({
  label: {
    marginBottom: 10,
  }
});


interface Props<T> {
  label: string;
  onChange: (value: string) => void;
  value?: T;
  error?: boolean;
  message?: string;
};

function TextFieldF2<T>(props: Props<T>) {
  const classes = useStyles();

  function onChange(event: React.ChangeEvent<HTMLInputElement>) {
    props.onChange(event.target.value);
  }

  return (
    <Grid container>
      <Grid item xs={12} classes={{item: classes.label}}>
        <FormLabel>{props.label}</FormLabel>

        <TextField
          error={props.error}
          helperText={props.message}
          fullWidth
          type='text'
          variant='filled'
          value={props.value}
          onChange={onChange}
        />
      </Grid>
    </Grid>
  );
}

export function TextFieldF2e<T>(props: Props<T>) {
  const classes = useStyles();

  function onChange(event: React.ChangeEvent<HTMLInputElement>) {
    props.onChange(event.target.value);
  }

  return (
    <Grid container>
      <Grid item xs={12} classes={{item: classes.label}}>
        <FormLabel>{props.label}</FormLabel>

        <TextField
          error={props.error}
          helperText={props.message}
          fullWidth
          type='email'
          variant='filled'
          value={props.value}
          onChange={onChange}
        />
      </Grid>
    </Grid>
  );
}

export default TextFieldF2