import React from 'react';
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import CloseIcon from '@material-ui/icons/Close';
// import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { makeStyles, Theme, useTheme } from '@material-ui/core/styles';

// import NavbarButton from './NavbarButton';
import { FinanservsHorizontalLogoImg } from '../../Images';
import { PanamanianFlagIcon } from '../../Svg';

const useStyles = makeStyles((theme: Theme) => ({
  logo: {
    width: 192,
    cursor: 'pointer',
    [theme.breakpoints.down('md')]: {
      width: 172,
      cursor: 'pointer',
      marginRight: 16,
    }
  },
  logIn: {
    color: theme.palette.success.main
  }
}));

interface Props {
  open: boolean;
  hasScrolled?: boolean;
  onLogoClick: () => void;
  onLogInClick?: () => void;
  onMenuClick: () => void;
};

function Navbar(props: Props) {
  const { open, onMenuClick } = props;
  const theme = useTheme();
  const desktop = useMediaQuery(theme.breakpoints.up('md'));
  const classes = useStyles();

  function renderLogo() {
    return <FinanservsHorizontalLogoImg className={classes.logo} onClick={props.onLogoClick} />;
  }

  function renderMobileMenu() {
    return (
      <Grid item>
        <IconButton color='inherit' onClick={onMenuClick}>
          { open ? <CloseIcon /> : <MenuIcon /> }
        </IconButton>
      </Grid>
    );
  }

  function renderLoginbutton() {
    return (
      <Grid item classes={{item: classes.logIn}}>
        {/* <NavbarButton
          onClick={onLogInClick}
          startIcon={<LockOutlinedIcon fontSize='small' color='inherit' />}
        >
          Iniciar sesión
        </NavbarButton> */}
      </Grid>
    );
  }

  return (
    <Grid container alignItems='center' justify='space-between'>
      <Grid item>
        {renderLogo()}
      </Grid>
      <Grid item container xs={3} sm={2} alignItems='center' justify='space-between'>
        {desktop && renderLoginbutton()}
        <PanamanianFlagIcon fontSize='large' />
        {!desktop && renderMobileMenu()}
      </Grid>
    </Grid>
  );
};

export default Navbar;
