import React from 'react';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import Input from '@material-ui/core/Input';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  label: {
    marginBottom: 24
  },
  input: {
    "&:invalid": {
      border: "red solid 2px"
    }
  }
});

interface Props<T> {
  label: string;
  onPhoneNumberChange: (value: string) => void;
  phoneNumber: T;
  onExtensionChange: (value: string) => void;
  extension: T;
};

function PhoneNumberField<T>(props: Props<T>) {
  const classes = useStyles();

  function onPhoneNumberChange(event: React.ChangeEvent<HTMLInputElement>) {
    props.onPhoneNumberChange(event.target.value);
  }

  function onExtensionChange(event: React.ChangeEvent<HTMLInputElement>) {
    props.onExtensionChange(event.target.value);
  }

  return (
    <Grid container>
      <Grid item xs={12} classes={{item: classes.label}}>
        <FormLabel>{props.label}</FormLabel>
      </Grid>
      <Grid container item xs={12} sm={6} spacing={2}>
        <Grid item xs={8}>
          <Input
            fullWidth
            type='tel'
            placeholder="333-4444"
            inputProps={{className: classes.input, pattern: "[0-9]{3}-[0-9]{4}"}}
            value={props.phoneNumber}
            onChange={onPhoneNumberChange}
          />
        </Grid>
        {!!props.onExtensionChange && (
          <Grid item xs={4}>
            <Input
              fullWidth
              type='text'
              inputProps={{className: classes.input, pattern: "[0-9]{3,4}"}}
              value={props.extension}
              onChange={onExtensionChange}
            />
          </Grid>
        )}
      </Grid>
    </Grid>
  );
}

export default PhoneNumberField;