import React, { useState, useEffect } from 'react';
import Container from '@material-ui/core/Container';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import { useHistory, useRouteMatch } from 'react-router-dom';
import NavBar from './Navbar';
import BackdropMenu from './BackdropMenu';
import PRODUCTS from '../../../lib/products';

const HOME_PATH = '/';
const LOGIN_PATH = '/inciar-sesion';

function ApplicationNavbar() {
  const [openBackdropMenu, setOpenBackdropMenu] = useState(false);

  const [scrollTargetId, setScrollTargetId] = useState('');
  let history = useHistory();
  let routeMatch = useRouteMatch({
    path: HOME_PATH,
    exact: true
  });

  useEffect(() => {
    if (scrollTargetId) {
      const target = document.querySelector(scrollTargetId);
  
      if (target) {
        target.scrollIntoView({ behavior: 'smooth', block: 'start' });
      }
    }

    return () => setScrollTargetId('');
  });

  function onAnchorClick(elementId: string) {
    if (!routeMatch) {
      history.push(HOME_PATH);
    } 

    setScrollTargetId(elementId);
  }

  function onLogoClick() {
    history.push(HOME_PATH);
  }

  function onLogInClick() {
    history.push(LOGIN_PATH);
  }

  function onDropdownOptionClick(path: string) {
    history.push(path);
  }

  return (
    <>
      <AppBar color='inherit'>
        <Toolbar disableGutters>
          <Container>
            <NavBar 
              open={openBackdropMenu}
              onLogInClick={onLogInClick}
              onLogoClick={onLogoClick} 
              onMenuClick={() => setOpenBackdropMenu(true)} 
            />
            <BackdropMenu 
              open={openBackdropMenu}
              dropdownOptions={Object.values(PRODUCTS)}
              onDropdownOptionClick={onDropdownOptionClick}
              onLogoClick={onLogoClick}
              onAnchorClick={onAnchorClick}
              onLogInClick={onLogInClick}
              onClose={() => setOpenBackdropMenu(false)}
            />
          </Container>
        </Toolbar>
      </AppBar>
      <Toolbar />
    </>
  );
}

export default ApplicationNavbar;

