import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';

import Form from './Form';

export interface IIncome {
  salario: number;
  gasto: number;
  inicio: string;
  meses_antig: number;
  cargo: string;
  entidad: string;
  contrato: string;
}

function Income(props: Props) {
  const cedula = window.localStorage.getItem('idPersonal')
  const { data: planilla, loading } = useFetch<IIncome[]>(
    `/planilla_css/${cedula}`,
    { method: "GET" },
    []
  );

  if (loading || !planilla) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form {...props} planilla={planilla} />;
};

export default Income;