import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import { PillRadioButtons } from '../../../../../components/RadioButtons';
import STRINGS from '../../../../../strings';
import { Props as ApplicationProps } from '../index';
import { MaritalStatus } from './index';

const { BORROWER } = STRINGS.APPLICATION.PERSONAL_LOAN;

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: 96
  }
});

interface Props extends ApplicationProps {
  maritalStatuses: MaritalStatus[]
}

function Form(props: Props) {
  const classes = useStyles();
  const { application: { borrower }, setApplication, maritalStatuses } = props;

  function onMaritalStatusSelected(maritalStatus: string) {
    setApplication({
      borrower: {
        maritalStatus
      },
    });
  }

  const options = maritalStatuses.map((maritalStatus) => ({
    label: maritalStatus.name,
    value: String(maritalStatus.id)
  }));


  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PillRadioButtons 
            label={BORROWER.MARITAL_STATUS}
            onClick={onMaritalStatusSelected}
            options={options}
            value={borrower.maritalStatus}
          />
        </Grid>
      </Grid>
    </Grid>
  );
}

export default Form;