import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

import STRINGS from '../../../../../strings';
import { PillRadioButtons } from '../../../../../components/RadioButtons';
import { TimeField } from '../../../../../components/Fields';

import { LABORAL_STATUS } from '../../types';
import { Props as BorrowerProps } from '../index';
import { LaboralStatus } from './index';
import { MRG_BOTT } from '../../types'

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: MRG_BOTT.bottomSpacing
  }
});

function isPermanent(contractType?: LABORAL_STATUS): boolean {
  return !!contractType && (contractType === LABORAL_STATUS.PERMANENT || contractType === LABORAL_STATUS.TEMPORARY);
}

interface Props extends BorrowerProps {
  laboralStatus: LaboralStatus[];
};

function Form(props: Props) {
  const classes = useStyles();
  const { quotation: { borrower }, setQuotation, laboralStatus } = props;

  function onContractTypeSelected(contractType: LABORAL_STATUS) {
    setQuotation({
      borrower: {
        contractType,
        currentJobMonths: undefined,
        previousJobMonths: undefined,
      },
    });
  }

  function onCurrentJobTime(currentJobMonths: string) {
    setQuotation({
      borrower: { currentJobMonths: Number(currentJobMonths) || undefined },
    });
  }

  const { contractType } = borrower;
  const jobTimeRequired = isPermanent(contractType);

  const options = laboralStatus?.map((status, idx) => ({
    label: status.name,
    value: String(idx)
  }));

  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PillRadioButtons 
            label={STRINGS.QUOTATION.PERSONAL_LOAN.EMPLOYMENT.CHOOSE_CONTRACT_TYPE}
            onClick={onContractTypeSelected}
            options={options || []}
            value={borrower.contractType}
          />
        </Grid>
        {jobTimeRequired && (
          <Grid item xs={12} sm={6} classes={{item: classes.bottomSpacing}}>
            <TimeField
              label={STRINGS.QUOTATION.PERSONAL_LOAN.EMPLOYMENT.CHOOSE_CONTRACT_TIME}
              onChange={onCurrentJobTime}
              value={borrower.currentJobMonths}
            />
          </Grid>
        )}
      </Grid>
    </Grid>
  );
};

export default Form;