import React, { useState, useEffect } from 'react'
import { Props } from '../views/Application/PersonalLoan/Forms'

function ReferApcPdf(props: Props) {
    const { application, setApplication } = props
    const { borrower  } = application
    const [fileName, setFileName] = useState('')

    const crearDatatoPdf = async (ID: any) => {
        const response: Response = await fetch("/upload/createPDF", {
            method: 'POST',
            body: new URLSearchParams({ "cedula": ID })
        })
        const data = await response.json()
        setFileName(data.fileName)
    }

    const enviarDatatoPdf = async (filename: string, id: string, ruta: string) => {

        const raw = {
            "fileName": filename,
            "entity_f": ruta,
            "prospect": borrower.id + ".pdf",
            "nameImage": "REFERENCIA-APC"
        }

        try {
            const uploadFileRes = await fetch(`/upload/file2`, {
                method: 'POST',
                body: new URLSearchParams(raw)
            })
    
            const uploadFileData = await uploadFileRes.json()

            setApplication({ documents: { apcReferencesUrl: uploadFileData.Location }})
            window.localStorage.setItem('application', JSON.stringify(application));
        } catch (e) {
            console.error(e);
        }
    }

    useEffect(() => {
        const { id } = borrower
        crearDatatoPdf(id)
    // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])

    useEffect(() => {
        if(fileName.length) {   
            const id = window.localStorage.getItem('idPersonal') || ''
            const ruta = window.localStorage.getItem('ruta') || ''
            enviarDatatoPdf(fileName, id, ruta)
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    },[fileName])

    return (
        <div>
        </div>
    )
  }
  
  export default ReferApcPdf;