import React, { useCallback } from 'react';
import SectorContext from "./index";
import { JOB_SECTORS } from '../../views/Quotation/PersonalLoan/types';

interface Props {
  children: JSX.Element | JSX.Element[]
}

export const  SectorProvider = ({ children }: Props) => {

  const handle = useCallback(async (jobSector: JOB_SECTORS) => {
    localStorage.setItem("jSector", jobSector);
  }, []);

  return (
    <SectorContext.Provider value={{
      handle
    }}>
      {children}
    </SectorContext.Provider>
  );
}