import React, { useContext, useState } from 'react';
import { useHistory } from "react-router-dom";
import Grid from '@material-ui/core/Grid';
import AccountBalanceOutlinedIcon from '@material-ui/icons/AccountBalanceOutlined';
import DomainOutlinedIcon from '@material-ui/icons/DomainOutlined';
import { CardRadioButtons } from '../../../../components/RadioButtons';
import { CaneIcon } from '../../../../components/Svg';
import SectorContext from '../../../../context/sector';
import { JOB_SECTORS } from '../types';

const options = [{
  label: 'Sector público',
  value: JOB_SECTORS.PUBLIC,
  Icon: AccountBalanceOutlinedIcon
}, {
  label: 'Sector privado',
  value: JOB_SECTORS.PRIVATE,
  Icon: DomainOutlinedIcon
}, {
  label: 'Jubilado',
  value: JOB_SECTORS.RETIRED,
  Icon: CaneIcon
}];

function JobSector() {
  let history = useHistory();
  const [sector, setSector] = useState<JOB_SECTORS>()
  const { handle } = useContext(SectorContext) 

  function onOptionPressed(jobSector: JOB_SECTORS): void {
    setSector(jobSector)
    handle(jobSector);
    history.push('/cotizar/prestamo-personal');
  };

  return (
    <Grid container spacing={2} justify='center'>
      <Grid item xs={11}>
        <CardRadioButtons 
          label={""} //{STRINGS.QUOTATION.PERSONAL_LOAN.COMPANY.CHOOSE_JOB_SECTOR}
          onClick={onOptionPressed}
          options={options}
          value={sector}
        />
      </Grid>
    </Grid>
  );
};

export default JobSector;