import React, { useState } from 'react';
import Grid from '@material-ui/core/Grid';
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';

import STRINGS from '../../strings';

function Faqs() {
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.up('sm'));
  const [expanded, setExpanded] = useState<number | boolean>(false);

  function onFaqClick(idx: number) {
    setExpanded(idx === expanded ? false : idx);
  };

  const questions = STRINGS.HOME.FAQ.QUESTIONS.map(({TITLE, CONTENT}, idx) => (
    <Faq key={idx} title={TITLE} content={CONTENT} expanded={expanded === idx} onClick={() => onFaqClick(idx)} />
  ));

  if (matches) {
    return (
      <Grid container spacing={2}>
        <Grid item xs={12} sm={6}>
          {questions.slice(0, 9).map((question, idx) => React.cloneElement(question, {key: idx}))}
        </Grid>
        <Grid item xs={12} sm={6}>
          {questions.slice(9, STRINGS.HOME.FAQ.QUESTIONS.length).map((question, idx) => React.cloneElement(question, {key: idx}))}
        </Grid>
      </Grid>
    );
  }

  return (
    <Grid container spacing={2}>
      <Grid item xs={12}>
        {questions.map((question, idx) => React.cloneElement(question, {key: idx}))}
      </Grid>
    </Grid>
  );
};

interface Props {
  title: string;
  content: string | JSX.Element | JSX.Element[];
  expanded: boolean;
  onClick: () => void;
};

function Faq(props: Props) {
  return (
    <Accordion expanded={props.expanded} onChange={props.onClick}>
      <AccordionSummary expandIcon={<ExpandMoreIcon color='primary' />}>
        <Typography component={'span'} color='primary'>{props.title}</Typography>
      </AccordionSummary>
      <AccordionDetails>
        <Typography component={'span'} color='secondary'>{props.content}</Typography>
      </AccordionDetails>
    </Accordion>
  );
}

export default Faqs;