import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Grid, FormLabel, Typography } from '@material-ui/core';

import STRINGS from '../../../../../strings';
import { CurrencyField } from '../../../../../components/Fields';
import { Props as BorrowerProps } from '../index';
import { IIncome } from './index';
import TextField12 from '../../../../../components/Fields/TextField12';
import { LABORAL_STATUS } from '../../types';

const useStyles = makeStyles({
    bottomSpacing: {
        marginBottom: 48
    },
    label: {
        marginBottom: 36
    },
    textField: {
        width: '14.5rem'
    }
});

interface Props extends BorrowerProps {
    planilla: IIncome[];
};

function Form(props: Props) {

    const classes = useStyles();
    const { quotation: { borrower }, setQuotation, planilla } = props;
    const { entidad='', cargo='', contrato='', inicio='', meses_antig=0, salario=0, gasto=0 } = planilla[0]
    const { wage, alloance } = borrower;

    function onWageChange(wage: string) {
        setQuotation({ borrower: { wage: Number(wage) || undefined } });
    }

    function onAlloanceChange(alloance: string) {
        setQuotation({ borrower: { alloance: Number(alloance) || undefined } });
    }

    function onPerDiemChange(perDiem: string) {
        setQuotation({ borrower: { perDiem: Number(perDiem) || undefined } });
    }

    useEffect(() => {
        setQuotation({ 
            borrower: { 
                wage: Number(salario) > 0 ? Number(salario) : wage || undefined, //entidad.length > 0 ? entidad : company || undefined,
                alloance: Number(gasto) > 0 ? Number(gasto) : alloance || undefined, //Number(gasto) || undefined,
                inicioLabores: inicio || undefined,
                mesesAntig: Number(meses_antig) || undefined,
                contractType: contrato === 'PERMANENTE' ? LABORAL_STATUS.PERMANENT : (contrato === '' ? borrower.contractType : LABORAL_STATUS.TEMPORARY),
            }
        });
        window.localStorage.setItem('entidad-cargo', entidad+'-'+cargo)
        // eslint-disable-next-line
    }, [])

    return (
        <Grid container spacing={2} justify='center'>
            <Grid item xs={11}>
                <Grid item xs={12} classes={{ item: classes.label }}>
                    <FormLabel>{STRINGS.QUOTATION.PERSONAL_LOAN.INCOME.CHOOSE_INCOME}</FormLabel>
                </Grid>
                <Grid item xs={12} sm={8} classes={{ item: classes.bottomSpacing }}>
                    <CurrencyField
                        label={STRINGS.QUOTATION.PERSONAL_LOAN.INCOME.WAGE}
                        onChange={onWageChange}
                        value={borrower.wage}
                    />
                </Grid>
                <Grid item xs={12} sm={8} classes={{ item: classes.bottomSpacing }}>
                    <CurrencyField
                        label={STRINGS.QUOTATION.PERSONAL_LOAN.INCOME.ALLOANCE}
                        onChange={onAlloanceChange}
                        value={borrower.alloance}
                    />
                </Grid>
                <Grid item xs={12} sm={8} classes={{ item: classes.bottomSpacing }}>
                    <CurrencyField
                        label={STRINGS.QUOTATION.PERSONAL_LOAN.INCOME.PER_DIEM}
                        onChange={onPerDiemChange}
                        value={borrower.perDiem}
                    />
                </Grid>
                <Grid item xs={12} classes={{ item: classes.label }}>
                    <Typography color='primary' noWrap={true}><u>Información adicional sobre su estado laboral</u></Typography>
                </Grid>
                <Grid item xs={12} sm={8} classes={{ item: classes.bottomSpacing }}>
                    <TextField12
                        label="Inicio de Laborees"
                        value={inicio.slice(0,10)}
                    />
                </Grid>
                <Grid item xs={12} sm={8} classes={{ item: classes.bottomSpacing }}>
                    <TextField12
                        label="Meses Antiguedad"
                        value={meses_antig}
                    />
                </Grid>
                <Grid item xs={12} sm={8} classes={{ item: classes.bottomSpacing }}>
                    <TextField12
                        label="Entidad"
                        value={entidad}
                    />
                </Grid>
                <Grid item xs={12} sm={8} classes={{ item: classes.bottomSpacing }}>
                    <TextField12
                        label="Cargo"
                        value={cargo}
                    />
                </Grid>
                <Grid item xs={12} sm={8} classes={{ item: classes.bottomSpacing }}>
                    <TextField12
                        label="Contrato"
                        value={contrato}
                    />
                </Grid>
            </Grid>
        </Grid>
    );
};

export default Form;