import React from 'react';
import Grid from '@material-ui/core/Grid';

import STRINGS from '../../../../../strings';
import { BankRadioButtons } from '../../../../../components/RadioButtons';
import { BacLogoImg, MultibankLogoImg, StGeorgeLogoImg, GlobalBankLogoImg, FinancomerLogoImg, PanacreditLogoImg, BanisiLogoImg } from '../../../../../components/Images';
import { Props } from '../index';
import { Opcion } from '../../types';
import PopUpEnding from '../../../../../components/PopUpEnding/index';


let options:any = [{}]

function Banks(props: Props) {
  const { quotation, setQuotation } = props;

  let Loans = quotation.loan.opciones || []

  function onOptionPressed(bank: string) {
    const wbank = Loans.filter(b => b.bank === bank )
    setQuotation({ loan: { bank, amount: wbank[0].loan, cashOnHand: wbank[0].cashOnHand } });
  }

  const calcBank = (loan: Opcion) => {
    let { 
      bank: ruta,
      term,
      monthlyFee,
      cashOnHand
    } = loan 

    options.push(
      {
        logo: ruta==='200' ? BacLogoImg 
            : (ruta==='300' ? MultibankLogoImg 
            : (ruta==='400' ? StGeorgeLogoImg 
            : (ruta==='500' ? GlobalBankLogoImg 
            : (ruta==='600' ? FinancomerLogoImg 
            : (ruta==='700' ? PanacreditLogoImg 
            : BanisiLogoImg ))))),
        value: ruta,
        amount: cashOnHand,
        payments: term,
        monthlyFee: monthlyFee
      }
    ) 
  }

  options=[]
  Loans.forEach(le => {
    // console.log(le)
    calcBank(le)
  })

  return (
    <Grid container spacing={2} justify='center'>
      <Grid item xs={11}>
        <BankRadioButtons
          label={STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.CHOOSE_BANK}
          options={options}
          onClick={onOptionPressed}
          value={quotation.loan.bank}
        />
        <PopUpEnding />
      </Grid>
    </Grid>
  );
};

export default Banks;