import { useState } from 'react';
import { Application } from '../types';

function getInitialApplication(): Application {
  return {
    tracking: undefined,
    documents: {
      idUrl: undefined,
      socialSecurityProofUrl: undefined,
      publicGoodProofUrl: undefined,
      workLetterUrl: undefined,
      payStubUrl: undefined,
      apcLetterUrl: undefined,
      apcReferencesUrl: undefined,
    },
    borrower: {
      id: undefined,
      maritalStatus: undefined,
      phoneNumber: undefined,
      province: undefined,
      district: undefined,
      county: undefined,
      street: undefined,
      barriada_edificio: undefined,
      no_casa_piso_apto: undefined,
      residentialNumber: undefined,
      company: undefined,
      position: undefined,
      companyAddress: undefined,
      companyPhoneNumber: undefined,
      companyPhoneExtension: undefined,
      previousCompany: undefined,
      wage: undefined,
      accept: undefined,
      contometro: undefined,
      endeudamiento: undefined,
      saldoPorDD: undefined,
      pjeCapEndeud: undefined,
      saldoDD: undefined,
      scoreAPC: undefined,
      aplicaPP: undefined,
      aplicaTC: undefined,
      aplicaAuto: undefined,
      aplicaHip: undefined,
      aplicaTCOk: undefined,
      aplicaAutoOk: undefined,
      aplicaHipOk: undefined,
      prestAuto: undefined,
      prestTC: undefined,
      prestHip: undefined,
    },
    sign: {
      signImage: undefined,
    },
    personalReferenceRelated: {
      name: undefined,
      lastName: undefined,
      relationship: undefined,
      phoneNumber: undefined,
      residenceNumber: undefined,
      company: undefined,
      companyPhoneNumber: undefined,
      companyPhoneExtension: undefined,
    },
    personalReferenceUnrelated: {
      name: undefined,
      lastName: undefined,
      phoneNumber: undefined,
      residenceNumber: undefined,
      company: undefined,
      companyPhoneNumber: undefined,
      companyPhoneExtension: undefined,
    },
    health1: {
      doctor: undefined,
      doctorSpecialty: undefined,
      healthCenter: undefined
    },
    health2: {
      question1: undefined,
      question2: undefined,
      question3: undefined,
      question4: undefined,
      question5: undefined,
      question6: undefined,
      question7: undefined,
      question8: undefined
    },
    health3: {
      question1: undefined,
      question2: undefined,
      question3: undefined,
      question4: undefined,
      question5: undefined,
      question6: undefined,
      question7: undefined,
      question8: undefined,
      question9: undefined
    }
  };
}

function useApplication(): [Application, (application: Partial<Application>) => void] {
  const [application, setApplication] = useState(getInitialApplication());

  function saveApplication(application: Partial<Application>): void {
    const {
      borrower,
      health1,
      health2,
      health3,
      personalReferenceRelated,
      personalReferenceUnrelated,
      documents,
      sign,
      tracking
    } = application;

    setApplication((prevApplication) => ({
      documents: {
        ...prevApplication.documents,
        ...documents,
      },
      borrower: {
        ...prevApplication.borrower,
        ...borrower,
      },
      personalReferenceRelated: {
        ...prevApplication.personalReferenceRelated,
        ...personalReferenceRelated,
      },
      personalReferenceUnrelated: {
        ...prevApplication.personalReferenceUnrelated,
        ...personalReferenceUnrelated,
      },
      health1: {
        ...prevApplication.health1,
        ...health1,
      },
      health2: {
        ...prevApplication.health2,
        ...health2,
      },
      health3: {
        ...prevApplication.health3,
        ...health3,
      },
      sign: {
        ...prevApplication.sign,
        ...sign,
      },     
      tracking 
    }));
  }

  return [application, saveApplication];
}

export default useApplication;