import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

import { PillRadioButtons } from '../../../../../components/RadioButtons';
import STRINGS from '../../../../../strings';
import { Props } from '../index';
import { MRG_BOTT } from '../../../../Quotation/PersonalLoan/types';

const HEALTH = STRINGS.APPLICATION.PERSONAL_LOAN.HEALTH_2;

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: MRG_BOTT.bottomSpacing
  }
});

const options = [{
  value: 'true',
  label: 'Si',
}, {
  value: 'false',
  label: 'No',
}];

function Health2(props: Props) {
  const classes = useStyles();
  const { application, setApplication } = props;

  function onApplicationChange(key: string, value: string) {
    setApplication({
      health2: {
        [key]: value === 'true'
      },
    });
  }

  return (
    <Grid container spacing={2} justify='center'>
      <Grid id="top" container item xs={12} sm={11} direction='column'>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PillRadioButtons 
            label={HEALTH.QUESTION_1}
            onClick={(value: string) => onApplicationChange('question1', value)}
            options={options}
            value={String(application.health2.question1)}
          />
        </Grid>
      </Grid>
      <Grid container item xs={12} sm={11} direction='column'>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PillRadioButtons 
            label={HEALTH.QUESTION_2}
            onClick={(value: string) => onApplicationChange('question2', value)}
            options={options}
            value={String(application.health2.question2)}
          />
        </Grid>
      </Grid>
      <Grid container item xs={12} sm={11} direction='column'>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PillRadioButtons 
            label={HEALTH.QUESTION_3}
            onClick={(value: string) => onApplicationChange('question3', value)}
            options={options}
            value={String(application.health2.question3)}
          />
        </Grid>
      </Grid>
      <Grid container item xs={12} sm={11} direction='column'>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PillRadioButtons 
            label={HEALTH.QUESTION_4}
            onClick={(value: string) => onApplicationChange('question4', value)}
            options={options}
            value={String(application.health2.question4)}
          />
        </Grid>
      </Grid>
      <Grid container item xs={12} sm={11} direction='column'>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PillRadioButtons 
            label={HEALTH.QUESTION_5}
            helper={HEALTH.QUESTION_HELPER_5}
            onClick={(value: string) => onApplicationChange('question5', value)}
            options={options}
            value={String(application.health2.question5)}
          />
        </Grid>
      </Grid>
      <Grid container item xs={12} sm={11} direction='column'>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PillRadioButtons 
            label={HEALTH.QUESTION_6}
            onClick={(value: string) => onApplicationChange('question6', value)}
            options={options}
            value={String(application.health2.question6)}
          />
        </Grid>
      </Grid>
      <Grid container item xs={12} sm={11} direction='column'>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PillRadioButtons 
            label={HEALTH.QUESTION_7}
            helper={HEALTH.QUESTION_HELPER_7}
            onClick={(value: string) => onApplicationChange('question7', value)}
            options={options}
            value={String(application.health2.question7)}
          />
        </Grid>
      </Grid>
      <Grid container item xs={12} sm={11} direction='column'>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PillRadioButtons 
            label={HEALTH.QUESTION_8}
            onClick={(value: string) => onApplicationChange('question8', value)}
            options={options}
            value={String(application.health2.question8)}
          />
        </Grid>
      </Grid>
    </Grid>
  );
}

export default Health2;