import React from 'react';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import Typography from '@material-ui/core/Typography';
import StarIcon from '@material-ui/icons/Star';
import { makeStyles, Theme } from '@material-ui/core/styles';

import STRINGS from '../../strings';

const useStyles = makeStyles((theme: Theme) => ({
  label: {
    marginBottom: 24
  },
  pill: {
    padding: 12,
    width: '100%',
    background: theme.palette.grey['200'],
    textTransform: 'uppercase',
    textAlign: 'center',
    borderRadius: 48,
    cursor: 'pointer'
  },
  selected: {
    background: theme.palette.primary.main,
    color: theme.palette.common.white
  },
  container: {
    paddingTop: 48,
    paddingBottom: 48,
    borderBottom: '1px solid',
    borderBottomColor: theme.palette.divider
  },
  amount: {
    fontWeight: 'bold'
  }
}));
interface RadioOption {
  logo: (props: React.ImgHTMLAttributes<HTMLImageElement>) => JSX.Element;
  amount: number | null;
  payments: number | null;
  monthlyFee: number | null;
  value: string;
};

interface Props {
  label: string;
  onClick: (value: string) => void;
  options: RadioOption[];
  value?: string;
};

function BankRadioButtons(props: Props) {
  const classes = useStyles();

  console.log('BankRadioButtons',props);
  return (
    <Grid container>
      <Grid item xs={12} classes={{item: classes.label}}>
        <FormLabel>{props.label}</FormLabel>
      </Grid>
      <Grid item xs={12} container spacing={2}>
        {props.options.length && props.options.map((option, idx) => {
          const highest = (idx === 0);
          const lowest = (idx === (props.options.length - 1));

          return (
            <Grid key={idx} container spacing={2} classes={{container: classes.container}} justify='space-between' alignItems='center'>
              <Grid item xs={12} sm={3}>
                <option.logo style={{height: 48}} />
                <div>
                  {highest && <StarIcon color='secondary' />}
                  <StarIcon color='secondary' />
                  {!lowest && <StarIcon color='secondary' />}
                </div>
              </Grid>
              <Grid item xs={4} sm={2}>
                <Typography gutterBottom>{STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.AMOUNT}</Typography>
                <Typography variant='h5' color='primary'>B/.{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(Number(option.amount))}</Typography>
              </Grid>
              <Grid item xs={4} sm={2}>
                <Typography gutterBottom>{STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.PAYMENTS}</Typography>
                <Typography variant='h5' align='center' color='primary'>{option.payments || '-'}</Typography>
              </Grid>
              <Grid item xs={4} sm={2}>
                <Typography gutterBottom>{STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.MONTHLY_FEE}</Typography>
                <Typography variant='h5' color='primary'>B/.{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(Number(option.monthlyFee))}</Typography>
              </Grid>
              <Grid item xs={12} sm={2}>
                <Typography 
                  onClick={() => props.onClick(option.value)}
                  variant='body2'
                  classes={{root: `${classes.pill} ${option.value === props.value && classes.selected}`}}
                >
                  <span>{STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.APPLY}</span>
                </Typography>
              </Grid>
            </Grid>
          );
        })}
      </Grid>
    </Grid>
  );
}

export default BankRadioButtons;