import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles, Theme } from '@material-ui/core/styles';
import { PillRadioButtons } from '../../../../../components/RadioButtons';
import { DigitalSignature } from '../DigitalSignature';

import { Props as ApplicationProps } from '../index';
import { iFecha } from './index';
// import ReferApcPdf from '../../../../../components/ReferApcPdf';

const useStyles = makeStyles((theme: Theme) => ({
  container: {
    display: 'flex', 
    flexDirection: 'column', 
    height: '100%', 
    paddingTop: 72, 
    paddingBottom: 72,
    [theme.breakpoints.down('sm')]: {
      paddingTop: 24, 
      paddingBottom: 24
    }
  },
  title: {
    display: 'flex',
    alignItems: 'center',
    marginBottom: 72
  },
  subtitle: {
    '& span': {
      fontWeight: theme.typography.fontWeightBold,
      color: theme.palette.primary.main
    }
  },
  content: {
    display: 'block',
    textAlign:'center',
    marginBottom: 40,
    justifyContent: 'center',
  },
  option: {
    display: 'block',
    justifyContent: 'center'
  }
}));

interface Props extends ApplicationProps {
  fecha: iFecha;
}

function Form(props: Props) {
  const classes = useStyles()
  const { application: { borrower }, setApplication, fecha } = props
  const nombre = window.localStorage.getItem('fullname') || ""
  const cedula = borrower.id || ""

  async function onSignedSelected(accept: number) {
    setApplication({
      borrower: { accept },
    });
    return 
  }

  const options = [
    { label: "Aceptar", value: 1 },
    { label: "Rechazar", value: 0 }
  ]

  return (
    <>
      <Grid container justify='center'>

        {/* <ReferApcPdf {...props} /> */}

        <Grid item xs={12} sm={8} classes={{item: classes.content}}>
          <Typography 
            align='center' 
            variant='h5' 
            paragraph
          >
          </Typography>
          <Typography 
            align='center' 
            variant='h4' 
            paragraph
          >
            Autorización de la APC
          </Typography>
          <Typography
              align='justify' 
              color='textSecondary'
          >
<Typography component={'span'}>
Panamá, {fecha.hoyes.split(',')[1]}
<br />
<br />
Señores<br />
<b>ACsoraT, S. A.</b><br />
Panamá<br />
<br />
<br />
<br />
Estimado señor: <b>{nombre}</b>
<br />
<br />
Por este medio autorizo(amos) expresamente a <b>ACsoraT, S. A.</b>, sus 
subsidiarias y/o afiliadas, cesionarios o sucesoras, así como cualquier compañía 
que por operación de cesión, administración o compra de cartera adquiera los 
derechos de mi crédito, a que de conformidad con lo expresado en el artículo 24 
y demás disposiciones aplicables de la Ley 24 de 22 de mayo de 2002, solicite, 
consulte, recopile, intercambie y transmita a cualquier agencia de información de 
datos,   bancos   o   agentes   económicos   informaciones   relacionadas   con 
obligaciones o transacciones crediticias que mantengo o pudiera mantener con 
dichos agentes económicos de la localidad, sobre mi(nuestros) historial de 
crédito y relaciones con acreedores.
También  queda  facultado  el  ACsoraT, S. A.,  sus  subsidiarias  y/o 
afiliadas, cesionarios o sucesoras, así como cualquier compañía que por una 
operación de cesión, administración o compra de cartera adquiera los derechos 
de   mi   crédito,   a   que   solicite   y   obtenga   información   de   instituciones 
gubernamentales relacionadas con las obligaciones o transacciones crediticias 
arriba referidas.
Así  mismo,  exonero(amos)  de  cualquier  consecuencia  o  responsabilidad resultante del ejercicio de solicitar o suministrar información, o por razón de cualesquiera autorizaciones contenidas en la presente carta, al ACsoraT, S. A, a sus compañías afiliadas, subsidiarias, cesionarios y/o sucesoras, a sus empleados, ejecutivos, directores dignatarios o apoderados, así como cualquier compañía que por una operación de cesión, administración o compra de cartera adquiera los derechos de mi crédito.
<br />
<br />
Atentamente,
<br />
<br />
<br />
<br />
Nombre: <u>{nombre}</u> 
<br />
Cédula: <u>{cedula}</u>
<br />
<br />
<br />
<br />
Fundamento legal: Ley 24 de 22 de mayo de 2002.
<br />
</Typography>
          </Typography>
          <DigitalSignature {...props}/>
        </Grid>

        <Grid item xs={12} sm={10} classes={{root: classes.option}}>
          <PillRadioButtons
            label={""}
            onClick={onSignedSelected}
            options={options}
            value={borrower.accept}
          />
        </Grid>
        
      </Grid>
    </>
  );
}

export default Form;