import React, { useState, useEffect } from 'react'
import { Props } from '../views/Application/PersonalLoan/Forms'

function AuthApcPdf(props: Props) {
    const { application, setApplication } = props
    const { borrower, sign  } = application
    const [fileName, setFileName] = useState('')

    const fullname = window.localStorage.getItem('fullname') || ""

    const crearDatatoPdf = async () => {
        const response: Response = await fetch("/upload/authApcPDF", {
            method: 'POST',
            body: new URLSearchParams({ 
                sign: sign.signImage?.toString() || "",
                nombre: fullname,
                cedula: borrower.id || ""
            })
        })
        const data = await response.json()
        setFileName(data.fileName)
    }

    const enviarDatatoPdf = async (filename: string, id: string, ruta: string) => {

        const raw = {
            "fileName": filename,
            "entity_f": ruta,
            "prospect": borrower.id + ".pdf",
            "nameImage": "CARTA-APC"
        }

        try {
            const uploadFileRes = await fetch(`/upload/file2`, {
                method: 'POST',
                body: new URLSearchParams(raw)
            })
    
            const uploadFileData = await uploadFileRes.json()
            
            setApplication({ documents: { apcLetterUrl: uploadFileData.Location }})
            window.localStorage.setItem('application', JSON.stringify(application))
        } catch (e) {
            console.error(e);
        }
    }

    useEffect(() => {
        crearDatatoPdf()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])

    useEffect(() => {
        if(fileName.length) {   
            const id = window.localStorage.getItem('idPersonal') || ''
            const ruta = window.localStorage.getItem('ruta') || ''
            enviarDatatoPdf(fileName, id, ruta)
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    },[fileName])

    return (
        <div>
        </div>
    )
  }
  
  export default AuthApcPdf;