import React from 'react';
import parse from 'html-react-parser';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import { makeStyles, Theme } from '@material-ui/core/styles';
import Hero from '../../components/Hero';
import STRINGS from '../../strings';

const useStyles = makeStyles((theme: Theme) => ({
  container: {
    paddingTop:96,
    paddingBottom:96
  },
  background: {
    backgroundColor: theme.palette.primary.light
  },
  spacing: {
    marginBottom:56
  }
}));

function Terms() {
  const classes = useStyles();

  return (
    <>
      <Hero>
        <Typography color='inherit' variant='h3' align='center'>{STRINGS.TERMS.TITLE}</Typography>
      </Hero>
      <Container classes={{root: classes.container}}>
        {STRINGS.TERMS.SECTIONS.map(({TITLE, CONTENT}, idx) => (
          <div key={idx} className={classes.spacing}>
            <Typography variant='h6' gutterBottom>{TITLE}</Typography>
            {CONTENT.map((content, idx) => 
              <Typography key={idx} component='span' paragraph>{parse(content)}</Typography>
            )}
          </div>
        ))}
      </Container>
      <div className={classes.background}>
        <Container classes={{root: classes.container}}>
          {STRINGS.TERMS.CONTACT.map(({TITLE, CONTENT}, idx) => (
            <div key={idx} className={classes.spacing}>
              <Typography variant='h6' gutterBottom>{TITLE}</Typography>
              <Typography paragraph>{parse(CONTENT)}</Typography>
            </div>
          ))}
        </Container>
      </div>
    </>
  );
};

export default Terms;