import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import { SvgIconProps } from '@material-ui/core/SvgIcon';
import { makeStyles, Theme } from '@material-ui/core/styles';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    cursor: 'pointer',
  },
  disabled: {
    cursor: 'unset',
  },
  content: {
    height: 50,
    textAlign: 'center',
    padding: 0
  },
  icon: {
    width: 72,
    height: 72
  },
  soon: {
    ...theme.typography.button,
    color:'white',
    backgroundColor: theme.palette.success.light,
    textAlign:'center',
    padding: 8
  },
  here: {
    ...theme.typography.button,
    color:'white',
    backgroundColor: theme.palette.secondary.main,
    textAlign:'center',
    padding: 8
  }
}));

interface Props {
  title: string;
  Icon: React.ElementType<SvgIconProps>;
  selected?: boolean;
  disabled?: boolean;
  onClick: () => void;
};

function ProductCard(props: Props) {
  const classes = useStyles();
  // const { Icon } = props;

  function handleClick() {
    if (!props.disabled) {
      props.onClick();
    }
  }

  return (
    <Card onClick={handleClick} classes={{root: `${classes.root} ${props.disabled && classes.disabled}`}}>
      <CardContent classes={{root: classes.content}}>
        {/* {Icon && <Icon color={props.disabled ? 'disabled' : 'primary'} classes={{root: classes.icon}} />} */}
        <Typography variant='button' align='center' color={props.disabled ? 'textSecondary' : 'inherit'}>
          {props.title}
        </Typography>
      </CardContent>
      {/* {props.disabled && (
        <div className={classes.soon}>Proximamente</div>
      )}
      {!props.disabled && (
        <div className={classes.here}>Click Aquí!</div>
      )} */}
    </Card>
  );
}

export default ProductCard;