import React from 'react';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import { makeStyles } from '@material-ui/core/styles';

import STRINGS from '../../strings';

const useStyles = makeStyles({
  label: {
    marginBottom: 24
  }
});

interface Props<T> {
  label: string;
  onChange: (value: string) => void;
  value: T;
};

function TimeField<T>(props: Props<T>) {
  const classes = useStyles();

  function onMonthsChange(event: React.ChangeEvent<HTMLInputElement>) {
    props.onChange(event.target.value);
  }

  return (
    <Grid container>
      <Grid item xs={12} classes={{item: classes.label}}>
        <FormLabel>{props.label}</FormLabel>
      </Grid>
      <Grid container spacing={2} item xs={12} >
        <Grid container item xs={10} sm={12} spacing={2}>
          <Grid item xs={6} sm={5}>
            <OutlinedInput
              type='number'
              value={props.value}
              onChange={onMonthsChange}
              placeholder='0'
            />
          </Grid>
          <Grid item>
            <br />
            <FormLabel>{STRINGS.FIELDS.TIME.MONTHS}</FormLabel>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
}

export default TimeField;