import { createMuiTheme, responsiveFontSizes } from '@material-ui/core/styles';

const LETTER_SPACING_MODIFIER = 0.05;
const LINE_HEIGHT_MODIFIER = 0.125;

const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#F7FAFD',
      main: '#0C7BDD',
      dark: '#0B3179',
    },
    secondary: {
      main: '#F19941',
      contrastText: '#ffffff'
    },
    background: {
      default: '#ffffff',
    },   
  },
  mixins: {
    toolbar: {
      minHeight: 72,
      '@media (min-width:0px) and (orientation: landscape)': {
        minHeight: 64,
      },
      '@media (min-width:600px)': {
        minHeight: 80,
      },
    },
  },
  typography: {
    h1: {
      lineHeight: 1.167 + LINE_HEIGHT_MODIFIER,
      letterSpacing: `${-0.01562 + LETTER_SPACING_MODIFIER}em`,
    },
    h2: {
      lineHeight: 1.2 + LINE_HEIGHT_MODIFIER,
      letterSpacing: `${-0.00833 + LETTER_SPACING_MODIFIER}em`,
    },
    h3: {
      lineHeight: 1.167 + LINE_HEIGHT_MODIFIER,
      letterSpacing: `${0 + LETTER_SPACING_MODIFIER}em`,
    },
    h4: {
      lineHeight: 1.235 + LINE_HEIGHT_MODIFIER,
      letterSpacing: `${0.00735 + LETTER_SPACING_MODIFIER}em`,
    },
    h5: {
      lineHeight: 1.334 + LINE_HEIGHT_MODIFIER,
      letterSpacing: `${0 + LETTER_SPACING_MODIFIER}em`,
    },
    h6: {
      lineHeight: 1.6 + LINE_HEIGHT_MODIFIER,
      letterSpacing: `${0.0075 + LETTER_SPACING_MODIFIER}em`,
    },
    subtitle1: {
      lineHeight: 1.75 + LINE_HEIGHT_MODIFIER,
      letterSpacing: `${0.00938 + LETTER_SPACING_MODIFIER}em`,
    },
    subtitle2: {
      lineHeight: 1.57 + LINE_HEIGHT_MODIFIER,
      letterSpacing: `${0.00714 + LETTER_SPACING_MODIFIER}em`,
    },
    body1: {
      lineHeight: 1.5 + LINE_HEIGHT_MODIFIER,
      letterSpacing: `${0.00938 + LETTER_SPACING_MODIFIER}em`,
    },
    body2: {
      lineHeight: 1.43 + LINE_HEIGHT_MODIFIER,
      letterSpacing: `${0.01071 + LETTER_SPACING_MODIFIER}em`,
    },
    button: {
      lineHeight: 1.75 + LINE_HEIGHT_MODIFIER,
      letterSpacing: `${0.02857 + LETTER_SPACING_MODIFIER}em`,
    },
    caption: {
      lineHeight: 1.66 + LINE_HEIGHT_MODIFIER,
      letterSpacing: `${0.03333 + LETTER_SPACING_MODIFIER}em`,
    },
    overline: {
      lineHeight: 2.66 + LINE_HEIGHT_MODIFIER,
      letterSpacing: `${0.08333 + LETTER_SPACING_MODIFIER}em`,
    },
  },
  overrides: {
    MuiButton: {
      root: {
        borderRadius: 48,
      },
    },
  },
});

export default responsiveFontSizes(theme);
