import React, { useEffect, useState } from 'react'
import axios from 'axios'
import NotData from './NotData'
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
require('dotenv').config();

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
    minHeight: 600,
    maxHeight: 440,
  },
});

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const Show = () => {
  const [prospects, setProspects] = useState([])
  const URL =  ''; //  process.env.REACT_APP_API_HOST;
  const classes = useStyles();
  const id_personal = localStorage.getItem('idPersonal') || ""

  useEffect(() => {
    // const id_personal = '7-094-486'
    if(id_personal)
      getByAproach(id_personal)
  },[id_personal])

  const getByAproach = async (id) => {
    const res = await axios.get(URL + '/adm/prospects/aproach/' + id)
    const data = await res.data
    setProspects(data)
  }

  return ( 
    <>
    <Paper className={classes.root}>
      <TableContainer className={classes.container}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
              <StyledTableCell align="center">ID</StyledTableCell>
              <StyledTableCell>Nombre</StyledTableCell>
              <StyledTableCell>Cedula</StyledTableCell>
              <StyledTableCell>Entidad</StyledTableCell>
              <StyledTableCell>Email</StyledTableCell>
              <StyledTableCell>Celular</StyledTableCell>
              {/* <StyledTableCell>IP</StyledTableCell>
              <StyledTableCell>CP</StyledTableCell>
              <StyledTableCell>FSS</StyledTableCell>
              <StyledTableCell>RS</StyledTableCell>
              <StyledTableCell>CT</StyledTableCell> */}
              <StyledTableCell>Estado</StyledTableCell>
              <StyledTableCell>Creado</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {(typeof(prospects) === "object") ? 
              prospects.map((row) => (
                <StyledTableRow align="center" key={row.id}>
                  <TableCell component="th" scope="row">
                    {row.id}
                  </TableCell>
                  <TableCell>{row.name}</TableCell>
                  <TableCell>{row.id_personal}</TableCell>
                  <TableCell>{row.entity}</TableCell>
                  <TableCell>{row.email}</TableCell>
                  <TableCell>{row.cellphone}</TableCell>
                  {/* <TableCell>{row.imag_id}</TableCell>
                  <TableCell>{row.imag_cp}</TableCell>
                  <TableCell>{row.imag_fss}</TableCell>
                  <TableCell>{row.imag_rs}</TableCell>
                  <TableCell>{row.imag_lw}</TableCell> */}
                  <TableCell>{row.estado}</TableCell>
                  <TableCell>{row.fcreate.slice(0,10)}</TableCell>
                </StyledTableRow>
              ))
            :
              <TableRow>
                <TableCell colSpan="8">
                <NotData />
                </TableCell>
              </TableRow>
            }
          </TableBody>
        </Table>
      </TableContainer>
    </Paper>
    </>
   )
}
 
export default Show