import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';

import Form from './Form';

export interface PaymentType {
  id: number;
  name: string;
  is_active: boolean;
}

function CreditHistory(props: Props) {
  
  const { data: paymentTypes, loading } = useFetch<PaymentType[]>(
    "/payment_types",
    { method: "GET" },
    []
  );

  if (loading || !paymentTypes) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form {...props} paymentTypes={paymentTypes} />;
};

export default CreditHistory;