import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import STRINGS from '../../../../../strings';
import { Props } from '../index';
import { MRG_BOTT } from '../../../../Quotation/PersonalLoan/types';
import TextFieldF from '../../../../../components/Fields/TextFieldF';

const { PERSONAL_REFERENCE_RELATED } = STRINGS.APPLICATION.PERSONAL_LOAN;

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: MRG_BOTT.bottomSpacing
  }
});

function PersonalReferenceRelated(props: Props) {
  const classes = useStyles();
  const { application: { personalReferenceRelated }, setApplication } = props;

  function onApplicationChange(key: string, value: string) {
    setApplication({
      personalReferenceRelated: {
        [key]: value
      },
    });
  }

  const cellRegex = /^\d{4}(-|\s)\d{4}$/
  // const phoneRegex = /^\d{3}(-|\s)\d{4}$/

  // const errExtension = () => {
  //   let Err = false
  //   if(personalReferenceRelated.companyPhoneExtension) {
  //     if(!personalReferenceRelated.companyPhoneExtension.match(/\d/g)) {
  //       Err = true
  //     } else if(personalReferenceRelated.companyPhoneExtension?.length > 4) {
  //       Err = true
  //     }
  //   }
  //   return Err
  // }

  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={PERSONAL_REFERENCE_RELATED.NAME}
            onChange={(value: string) => onApplicationChange('name', value)}
            value={personalReferenceRelated.name}
            error={!personalReferenceRelated.name || personalReferenceRelated.name.length < 3 || personalReferenceRelated.name.length > 20}
            message={!personalReferenceRelated.name ? "Este es un campo Obligatorio ... Mínimo 3 letras": (personalReferenceRelated.name.length > 20 ? "Máximo 20 caracteres." : (personalReferenceRelated.name.length < 3 ? "Mínimo 3 caracteres." : ""))}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={PERSONAL_REFERENCE_RELATED.LAST_NAME}
            onChange={(value: string) => onApplicationChange('lastName', value)}
            value={personalReferenceRelated.lastName}
            error={!personalReferenceRelated.lastName || personalReferenceRelated.lastName.length > 20}
            message={!personalReferenceRelated.lastName ? "Este es un campo Obligatorio ...": (personalReferenceRelated.lastName.length > 20 ? "Máximo 20 caracteres." : "")}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={PERSONAL_REFERENCE_RELATED.RELATIONSHIP}
            onChange={(value: string) => onApplicationChange('relationship', value)}
            value={personalReferenceRelated.relationship}
            error={!personalReferenceRelated.relationship || personalReferenceRelated.relationship.length > 10}
            message={!personalReferenceRelated.relationship ? "Este es un campo Obligatorio ...": (personalReferenceRelated.relationship.length > 10 ? "Máximo 10 caracteres." : "")}
          />
        </Grid>
        {/* <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={PERSONAL_REFERENCE_RELATED.RESIDENCE_PHONE_NUMBER}
            onChange={(value: string) => onApplicationChange('residenceNumber', value)}
            value={personalReferenceRelated.residenceNumber}
            error={!personalReferenceRelated.residenceNumber || personalReferenceRelated.residenceNumber.length > 8 || !phoneRegex.test(personalReferenceRelated.residenceNumber)}
            message={!personalReferenceRelated.residenceNumber ? "Este es un campo Obligatorio ...": (personalReferenceRelated.residenceNumber.length > 8 ? "Máximo 7 números." : (!phoneRegex.test(personalReferenceRelated.residenceNumber) ? "Ej. 320-2323" : ""))}
          />
        </Grid> */}
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={PERSONAL_REFERENCE_RELATED.PHONE_NUMBER}
            onChange={(value: string) => onApplicationChange('phoneNumber', value)}
            value={personalReferenceRelated.phoneNumber}
            error={!personalReferenceRelated.phoneNumber || personalReferenceRelated.phoneNumber.length > 9 || !cellRegex.test(personalReferenceRelated.phoneNumber)}
            message={!personalReferenceRelated.phoneNumber ? "Este es un campo Obligatorio ...": (personalReferenceRelated.phoneNumber.length > 9 ? "Máximo 8 números." : (!cellRegex.test(personalReferenceRelated.phoneNumber) ? "Ej. 6320-2323" : ""))}
          />
        </Grid>

        {/* <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={PERSONAL_REFERENCE_RELATED.COMPANY}
            onChange={(value: string) => onApplicationChange('company', value)}
            value={personalReferenceRelated.company}
            error={!personalReferenceRelated.company || personalReferenceRelated.company.length > 80}
            message={!personalReferenceRelated.company ? "Este es un campo Obligatorio ...": (personalReferenceRelated.company.length > 80 ? "Máximo 80 caracteres." : "")}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <Grid container item xs={12} sm={6} spacing={2}>
            <Grid item xs={8}>
              <TextFieldF
                label={PERSONAL_REFERENCE_RELATED.COMPANY_PHONE_NUMBER}
                onChange={(value: string) => onApplicationChange('companyPhoneNumber', value)}
                value={personalReferenceRelated.companyPhoneNumber}
                error={!personalReferenceRelated.companyPhoneNumber || personalReferenceRelated.companyPhoneNumber.length > 8 || !phoneRegex.test(personalReferenceRelated.companyPhoneNumber)}
                message={!personalReferenceRelated.companyPhoneNumber ? "Este es un campo Obligatorio ...": (personalReferenceRelated.companyPhoneNumber.length > 8 ? "Máximo 7 números." : (!phoneRegex.test(personalReferenceRelated.companyPhoneNumber) ? "Ej. 320-2323" : ""))}
              />
            </Grid>
            <Grid item xs={4}>
              <TextFieldF
                label={"Extensión"}
                onChange={(value: string) => onApplicationChange('companyPhoneExtension', value)}
                value={personalReferenceRelated.companyPhoneExtension}
                error={errExtension()}
                message={(personalReferenceRelated.companyPhoneExtension && personalReferenceRelated.companyPhoneExtension.length > 4 ? "Máximo 4 números." : (personalReferenceRelated.companyPhoneExtension && !personalReferenceRelated.companyPhoneExtension.match(/\d/g) ? "Sólo números" : ""))}
              />
            </Grid>
          </Grid>
        </Grid> */}
      </Grid>
    </Grid>
  );
}

export default PersonalReferenceRelated;