import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

import STRINGS from '../../../../../strings';
import { Props } from '../index';
import { MRG_BOTT } from '../../../../Quotation/PersonalLoan/types';
import TextFieldF from '../../../../../components/Fields/TextFieldF';

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: MRG_BOTT.bottomSpacing
  }
});

function Health1(props: Props) {
  const classes = useStyles();
  const { application, setApplication } = props;

  function onApplicationChange(key: string, value: string) {
    setApplication({
      health1: {
        [key]: value
      },
    });
  }

  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={STRINGS.APPLICATION.PERSONAL_LOAN.HEALTH_1.DOCTOR}
            onChange={(value: string) => onApplicationChange('doctor', value)}
            value={application.health1.doctor}
            error={!application.health1.doctor}
            message={!application.health1.doctor ? "Este es un campo Obligatorio ...": ""}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={STRINGS.APPLICATION.PERSONAL_LOAN.HEALTH_1.DOCTOR_SPECIALTY}
            onChange={(value: string) => onApplicationChange('doctorSpecialty', value)}
            value={application.health1.doctorSpecialty}
            error={!application.health1.doctorSpecialty}
            message={!application.health1.doctorSpecialty ? "Este es un campo Obligatorio ...": ""}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={STRINGS.APPLICATION.PERSONAL_LOAN.HEALTH_1.HEALTH_CENTER}
            onChange={(value: string) => onApplicationChange('healthCenter', value)}
            value={application.health1.healthCenter}
            error={!application.health1.healthCenter}
            message={!application.health1.healthCenter ? "Este es un campo Obligatorio ...": ""}
          />
        </Grid>
      </Grid>
    </Grid>
  );
}

export default Health1;