import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import {
  BanisiDesaturatedLogoImg,
  FinancomerDesaturatedLogoImg,
  PanacreditDesaturatedLogoImg,
  FinacreditDesaturatedLogoImg,
} from '../../components/Images';

// BacDesaturatedLogoImg,
// StGeorgeDesaturatedLogoImg,
// GlobalBankDesaturatedLogoImg,
// MultibankDesaturatedLogoImg,

const useStyles = makeStyles({
  allyImg: {
    opacity:0.72,
    height:36,
    width:150
  }
});

const ALLIES = [
  // BacDesaturatedLogoImg,
  // GlobalBankDesaturatedLogoImg,
  // StGeorgeDesaturatedLogoImg,
  // MultibankDesaturatedLogoImg,
  PanacreditDesaturatedLogoImg,
  FinancomerDesaturatedLogoImg,
  BanisiDesaturatedLogoImg,
  FinacreditDesaturatedLogoImg,
];

function Allies() {
  const classes = useStyles();

  return (
    <Grid container justify='center' spacing={6}>
      {ALLIES.map((Ally, idx) => (
        <Grid  key={idx} item>
          <Ally className={classes.allyImg} />
        </Grid>
      ))}
    </Grid>
  );
}

export default Allies;