import React, { useState } from 'react';
import { makeStyles, Theme } from '@material-ui/core/styles';
import { Button, Grid, Modal } from '@material-ui/core';
import FaltaPoco from '../../components/Images/images/pop-picture-falta-poco.jpg'

const useStyles = makeStyles((theme: Theme) => ({
    modal: {
      opacity: '0.8',
      background: `url(${FaltaPoco})`,
      border: "2px solid #0ca6ee",
      backgroundPosition: 'center',
      backgroundRepeat: 'no-repeat',
      boxShadow: theme.shadows[5],
      transform: "tranlate(-50%, -50%)",
      backgroundSize: "100%",
      [theme.breakpoints.down('lg')]: {
        height: '675px',
        cursor: 'pointer',
        margin: '55px auto'
      },
      [theme.breakpoints.down('xs')]: {
        height: '550px',
        cursor: 'pointer',
        position:'absolute',
        top:'5%',
        left:'5%',
        width: '90%',
      }
    },
    boton: {
        [theme.breakpoints.down('xs')]: {
            height: '400px', 
            width: '600px',
        },
        [theme.breakpoints.down('lg')]: {
            height: '800px', 
            width: '370px',
            margin: 'auto'
        }
    }

}));


function PopUpEnding(props: any) {
  
    const styles = useStyles()
    const [modal, setModal] = useState(true)

    const abrircerrarmodal = () => {
        setModal(!modal)
    }

    const body = (
        <Grid container spacing={2}>
            <Button
                    onClick={()=>abrircerrarmodal()}
                    className={styles.boton} 
                >
                <Grid item xs={12} className={styles.modal}>
                </Grid>
            </Button>
        </Grid>
    )
  
    return (
        <div>
            <Modal
                open={ modal }
                onClose={()=>abrircerrarmodal()}
            >
                {body}
            </Modal>
        </div>
    )
  }
  

  export default PopUpEnding;