import React, { useState } from 'react';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { SvgIconProps } from '@material-ui/core/SvgIcon';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Typography from '@material-ui/core/Typography';
import { makeStyles, Theme } from '@material-ui/core/styles';
import NavbarButton from './NavbarButton';

interface OptionProps {
  name: string;
  icon: React.ElementType<SvgIconProps>;
  path: string;
  disabled: boolean;
}

interface Props {
  hasScrolled: boolean;
  options?: OptionProps[];
  children: React.ReactChild;
  onClick?: any;
  startIcon?: React.ReactElement;
  endIcon?: React.ReactElement;
};

const useStyles = makeStyles((theme: Theme) => ({
  menuItem: {
    height:64,
    paddingLeft:24,
    paddingRight:24
  }
}));

function NavbarDropdown(props: Props) {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = useState<HTMLElement | null>(null);

  function openMenu(event: React.MouseEvent<HTMLElement>) {
    setAnchorEl(event.currentTarget);
  };

  function closeMenu() {
    setAnchorEl(null);
  };

  function onOptionClick(path: string) {
    props.onClick(path);
    closeMenu();
  }

  return (
    <>
      <NavbarButton 
        hasScrolled={props.hasScrolled}
        onClick={openMenu}
        startIcon={props.startIcon}
        endIcon={props.endIcon}
      >
        {props.children}
      </NavbarButton>
      {props.options && (
        <Menu
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={closeMenu}
        >
          {props.options.map((option, idx) => (
            <MenuItem
              key={idx}
              classes={{root: classes.menuItem}}
              onClick={() => onOptionClick(option.path)}
              disabled={option.disabled}
            >
                {option.icon && (
                  <ListItemIcon>
                    <option.icon color='primary' />
                  </ListItemIcon>
                )}
                <Typography>{option.name}</Typography>
            </MenuItem>
          ))}
        </Menu>
      )}
    </>
  );
}

export default NavbarDropdown;