import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

import STRINGS from '../../../../../strings';
import { PillRadioButtons } from '../../../../../components/RadioButtons';

import { PaymentType } from './index';
import { Props as BorrowerProps } from '../index';
import { MRG_BOTT } from '../../types'

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: MRG_BOTT.bottomSpacing
  }
});
 
const creditHistoryOptions = [{
  value: 'true',
  label: 'Si',
}, {
  value: 'false',
  label: 'No',
}];

interface Props extends BorrowerProps {
  paymentTypes: PaymentType[]
};
 
function Form(props: Props) {
  const classes = useStyles();
  const { quotation: { borrower }, setQuotation, paymentTypes } = props;

  function onCreditHistorySelected(creditHistory: string) {
    const hasCreditHistory = creditHistory === "true";
    setQuotation({
      borrower: {
        creditHistory: hasCreditHistory,
        paymentFrecuency: undefined,
      },
    });
  }

  function onPaymentFrecuencySelected(paymentFrecuency: string) {
    setQuotation({
      borrower: { paymentFrecuency },
    });
  }

  const options = paymentTypes.map((paymentType) => ({
    label: paymentType.name,
    value: String(paymentType.id)
  }));
 
  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PillRadioButtons 
            label={STRINGS.QUOTATION.PERSONAL_LOAN.CREDIT_HISTORY.CHOOSE_CREDIT_HISTORY}
            helper={STRINGS.QUOTATION.PERSONAL_LOAN.CREDIT_HISTORY.CHOOSE_CREDIT_HISTORY_HELPER}
            onClick={onCreditHistorySelected}
            options={creditHistoryOptions}
            value={String(borrower.creditHistory)}
          />
        </Grid>
        {borrower.creditHistory && (
          <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
            <PillRadioButtons 
              label={STRINGS.QUOTATION.PERSONAL_LOAN.CREDIT_HISTORY.CHOODE_PAYMENT_FRECUENCY}
              onClick={onPaymentFrecuencySelected}
              options={options}
              value={borrower.paymentFrecuency}
            />
          </Grid>
        )}
      </Grid>
    </Grid>
  );
};

export default Form;