import React, { useState } from 'react';
import Backdrop from '@material-ui/core/Backdrop';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Collapse from '@material-ui/core/Collapse';
import Container from '@material-ui/core/Container';
// import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
// import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import { makeStyles, Theme } from '@material-ui/core/styles';

import Navbar from './Navbar';

import PRODUCTS from '../../../lib/products';

const useStyles = makeStyles((theme: Theme) => ({
  logIn: {
    height: 96,
    display: 'flex',
    alignItems: 'center',
    color: theme.palette.success.main,
  },
  logInButton: {
    display: 'flex',
    alignItems: 'center'
  },
  logInIcon: {
    marginRight: 12
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    background: theme.palette.common.white,
    justifyContent: 'flex-start',
    flexDirection: 'column'
  },
  toolbar: {
    width: '100%',
    borderBottom: '1px solid',
    borderBottomColor: theme.palette.divider
  },
  menu: {
    width: '100%',
    height: '100%',
    overflow: 'scroll',
    borderBottom: '1px solid',
    borderBottomColor: theme.palette.divider
  },
  menuItem: {
    minHeight: 64
  }
}));

interface Props {
  open: boolean;
  dropdownOptions: Array<typeof PRODUCTS['PERSONAL_LOAN']>;
  onDropdownOptionClick: (path: string) => void;
  onAnchorClick: (elementId: string) => void;
  onLogoClick: () => void;
  onLogInClick: () => void;
  onClose: () => void;
};

function BackdropMenu(props: Props) {
  const classes = useStyles();
  const [dropdown, setDropdown] = useState(true);

  function onDropdownOptionClick(path: string) {
    props.onClose();
    props.onDropdownOptionClick(path);
  }

  function onAnchorClick(elementId: string) {
    props.onClose();
    props.onAnchorClick(elementId);
  }

  function onLogoClick() {
    props.onClose();
    props.onLogoClick();
  }

  return (
    <Backdrop classes={{root: classes.backdrop}} open={props.open}>
      <Toolbar disableGutters classes={{root: classes.toolbar}}>
        <Container>
          <Navbar open={props.open} onMenuClick={props.onClose} onLogoClick={onLogoClick} />
        </Container>
      </Toolbar>
      <div className={classes.menu}>
        <List>
            <ListItem key="1" button onClick={() => setDropdown(!dropdown)} classes={{root: classes.menuItem}}>
              <ListItemText primary="Compara" />
              {dropdown ? <ExpandLessIcon /> : <ExpandMoreIcon />}
            </ListItem>
            <Collapse in={dropdown} timeout="auto" unmountOnExit>
              <List component="div" disablePadding>
                {props.dropdownOptions.map((option, idx) => (
                  <ListItem
                    key={idx}
                    disabled={option.disabled}
                    divider
                    button
                    onClick={()=> onDropdownOptionClick(option.path)} 
                    classes={{root: classes.menuItem}}
                  >
                    <ListItemIcon>
                      <option.icon color='primary'/>
                    </ListItemIcon>
                    <ListItemText primary={option.name} />
                  </ListItem>
                ))}
              </List>
            </Collapse>
            <ListItem key="2" button classes={{root: classes.menuItem}} onClick={() => onAnchorClick('#steps')}>
              <ListItemText primary='¿Cómo empezar?' />
            </ListItem>
            <ListItem key="3" button classes={{root: classes.menuItem}} onClick={() => onAnchorClick('#faqs')}>
              <ListItemText primary='Preguntas frecuentes' />
            </ListItem>
        </List>
      </div>
      <div className={classes.logIn}>
        {/* <Typography align='center' classes={{root: classes.logInButton}}>
          <LockOutlinedIcon fontSize='small' classes={{root: classes.logInIcon}}/>
          Iniciar sesión
        </Typography> */}
      </div>
    </Backdrop>
  );
}

export default BackdropMenu;