import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';
import Form from './Form';
import { iApc, LaboralEntity } from '../../../../../interfaces';


export interface LaboralSector {
  id: number;
  sector: string;
  id_sector: number,
  name: string;
}


function Aproach(props: Props) {

  const { application: { borrower: { id } } } = props;

  //"/leerAPC",
  const { data: refApc, loading } = useFetch<iApc[]>(
    "/APC",
    { 
      method: "POST",
      body: JSON.stringify({ "id": id, tipoCliente: "1", productoApc: "1" })
    },
    []
  );

  const { data: laboralEntitys, loading: loadEntitys } = useFetch<LaboralEntity[]>(
    "/laboral_sector_entity_f",
    { method: "GET" },
    []
  );

  const { data: sectorResponse, loading: sectorLoading, } = useFetch<LaboralSector[]>(
    '/laboral_sector', 
    { method: 'GET' }, 
    []
  );

  
  if (loading || !refApc || loadEntitys || !laboralEntitys || sectorLoading || !sectorResponse) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form 
    {...props} 
    refApc={refApc} 
    laboralEntitys={laboralEntitys} 
    sectors={sectorResponse}
  />;
};

export default Aproach;