import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';

import Form from './Form';

export interface MaritalStatus {
  id: number;
  name: string;
  is_active: boolean;
}

function Borrower(props: Props) {
  const { data: maritalStatuses, loading } = useFetch<MaritalStatus[]>(
    "/marital_status",
    { method: "GET" },
    []
  );

  if (loading || !maritalStatuses) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form {...props} maritalStatuses={maritalStatuses} />;
};

export default Borrower;