/* eslint-disable @typescript-eslint/no-unused-vars */
import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

// eslint-disable-next-line @typescript-eslint/no-unused-vars
import FacebookLogin from 'react-facebook-login';
import GoogleLogin from 'react-google-login';
import { Button, Typography } from '@material-ui/core';
import EmailOutlined from '@material-ui/icons/EmailOutlined';

import { LOGIN_FB_G, MRG_BOTT } from '../types';
import { Props } from './index';

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: MRG_BOTT.bottomSpacing
  },
  botton: {
    padding: '10px 40px',
    color: 'rgba(0,0,0,0.54)'
  }
});


function Register(props: Props) {
  const classes = useStyles();
  const { quotation: { user }, setQuotation } = props;

  const responseFacebook = (response: any) => {
    let wfname = ''
    let wlname = ''
    if (response.status === 'connected') {
      let nameFull = response.name.split(' ')

      if (nameFull.length === 2) {
        wfname = nameFull[0]
        wlname = nameFull[1]
      } else if (nameFull.length === 4) {
        wfname = nameFull[0] + ' ' + nameFull[1]
        wlname = nameFull[2] + ' ' + nameFull[3]
      } else if (nameFull.length === 3) {
        wfname = nameFull[0] + ' ' + nameFull[1]
        wlname = nameFull[2] 
      } else if (nameFull.length === 1) {
        wfname = nameFull[0]
      }
      setQuotation({
        user: {
          email: response.email,
          name: response.name,
          fname: wfname,
          lname: wlname,
          picture: response.picture.data.url,
          origin: "fb",
          idUser: response.id
        },
      });
    } else if (response.name) {
        let nameFull = response.name.split(' ')

        if (nameFull.length === 2) {
          wfname = nameFull[0]
          wlname = nameFull[1]
        } else if (nameFull.length === 4) {
          wfname = nameFull[0] + ' ' + nameFull[1]
          wlname = nameFull[2] + ' ' + nameFull[3]
        } else if (nameFull.length === 3) {
          wfname = nameFull[0] + ' ' + nameFull[1]
          wlname = nameFull[2] 
        } else if (nameFull.length === 1) {
          wfname = nameFull[0]
        }        
        setQuotation({
          user: {
            email: response.email,
            name: response.name,
            fname: wfname,
            lname: wlname,
            picture: response.picture.data.url,
            origin: "fb",
            idUser: response.id
          },
        });
    } else {
      console.log('Facebook-Login:', response);
    }
  }

  const responseGoogle = (response: any) => {
    setQuotation({
      user: {
        email: response.profileObj.email,
        name: response.profileObj.name,
        fname: response.profileObj.givenName,
        lname: response.profileObj.familyName,
        picture: response.profileObj.imageUrl,
        origin: "google",
        idUser: response.profileObj.googleId
      },
    });
  }

  const rejectGoogle = (response: any) => {
    console.log('Google-Login:', response);
  }

  const orienEmail = () => {
    setQuotation({
      user: {
        origin: "email",
      }
    })
  }

  return (
      <Grid container spacing={2} justify='center'>
        <Grid container item xs={12} sm={11} direction='column'>
          <Grid item xs={12} sm={6} classes={{item: classes.bottomSpacing}}>
            <Typography variant="h5">Registro de Usuario con: </Typography>
            <Typography>{user.name}</Typography>
            <Typography>{user.email}</Typography>
            <img src={user.picture} alt={user.fname}/>
          </Grid>
        </Grid> 
        <Grid container spacing={2} justify='center' direction='row'>
          <Grid item xs={12} sm={11}>
          <Typography variant="h5">Estamos por aqui</Typography>
            <FacebookLogin
              appId={LOGIN_FB_G.FB}
              autoLoad={false}
              fields="name,email,picture"
              callback={responseFacebook} 
              textButton="Iniciar Sesión con Facebook"
              icon="fa-facebook"
            />
          </Grid>
          {/* <Grid item xs={12} sm={11}>
            <GoogleLogin
              clientId={LOGIN_FB_G.GOOGLE}
              buttonText="Iniciar Sesión con Google"
              onSuccess={responseGoogle}
              onFailure={rejectGoogle}
              cookiePolicy={'single_host_origin'}
              isSignedIn={true}
            />
          </Grid> */}
          <Grid item xs={12} sm={11}>
            <Button 
              classes={{root:classes.botton}}
              variant="outlined" 
              startIcon={<EmailOutlined />} 
              onClick={()=>orienEmail()}
            >
              Confirmar por email
            </Button>
          </Grid>
        </Grid>
      </Grid>
  );
};

export default Register;