import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import { MRG_BOTT } from '../../types';
import Select from '@material-ui/core/Select';
import { Nationality } from './index';
import { Props as QuotationProps } from '../index';
import MenuItem from '@material-ui/core/MenuItem';
import InputLabel from '@material-ui/core/InputLabel';
import TextFieldF from '../../../../../components/Fields/TextFieldF';

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: MRG_BOTT.bottomSpacing
  }
});

interface Props extends QuotationProps {
  nationalities: Nationality[];
};

function Form(props: Props) {
  const classes = useStyles();
  const { quotation, setQuotation, nationalities } = props;

  function onApplicationChange(key: string, value: string) {
    setQuotation({
      user: { [key]: value }})     
  }

  function onNationChange(event: React.ChangeEvent<{ value: unknown }>) {
    const nation = event.target.value as string
    setQuotation({
      user: { nationality: nation }})
  }

  const emailRegex = /^[-\w.%+]{1,64}@(?:[A-Z0-9-]{1,63}\.){1,125}[A-Z]{2,63}$/i
  const cellRegex = /^\d{4}(-|\s)\d{4}$/

  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label="Primer Nombre:"
            onChange={(value: string) => onApplicationChange('fname', value)}
            value={quotation.user.fname}
            error={!quotation.user.fname || quotation.user.fname.length > 20}
            message={!quotation.user.fname ? "Este es un campo Obligatorio ...": (quotation.user.fname.length > 20 ? "Máximo 20 caracteres." : "")}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label="Segundo Nombre:"
            onChange={(value: string) => onApplicationChange('fname_2', value)}
            value={quotation.user.fname_2}
            error={!!quotation.user.fname_2 && quotation.user.fname_2?.length > 20}
            message={!!quotation.user.fname_2 && quotation.user.fname_2?.length > 20 ? "Máximo 20 caracteres." : ""}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label="Primer Apellido:"
            onChange={(value: string) => onApplicationChange('lname', value)}
            value={quotation.user.lname}
            error={!quotation.user.lname || quotation.user.lname.length > 20}
            message={!quotation.user.lname ? "Este es un campo Obligatorio ...": (quotation.user.lname.length > 20 ? "Máximo 20 caracteres." : "")}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label="Segundo Apellido:"
            onChange={(value: string) => onApplicationChange('lname_2', value)}
            value={quotation.user.lname_2}
            error={!quotation.user.lname_2 || quotation.user.lname_2.length > 20}
            message={!quotation.user.lname_2 ? "Este es un campo Obligatorio ...": (quotation.user.lname_2.length > 20 ? "Máximo 20 caracteres." : "")}            
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label="Email:"
            onChange={(value: string) => onApplicationChange('email', value)}
            value={quotation.user.email}
            error={!quotation.user.email || quotation.user.email.length > 60 || !emailRegex.test(quotation.user.email)}
            message={!quotation.user.email ? "Este es un campo Obligatorio ...": (quotation.user.email.length > 60 ? "Máximo 60 caracteres." : (!emailRegex.test(quotation.user.email) ? "Ej. correo@abc.com" : ""))}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label="Celular #:"
            onChange={(value: string) => onApplicationChange('cellphone', value)}
            value={quotation.user.cellphone}
            error={!quotation.user.cellphone || quotation.user.cellphone.length > 9 || !cellRegex.test(quotation.user.cellphone)}
            message={!quotation.user.cellphone ? "Este es un campo Obligatorio ...": (quotation.user.cellphone.length > 9 ? "Máximo 8 números." : (!cellRegex.test(quotation.user.cellphone) ? "Ej. 6689-2323" : ""))}
          />
        </Grid>

        <Grid container item xs={12} sm={6} spacing={2} classes={{item: classes.bottomSpacing}}>
          <InputLabel id="">{"Nacionalidad:"}</InputLabel>
          <Select
            fullWidth
            type='text'
            value={quotation.user.nationality}
            onChange={onNationChange}
          >
            {nationalities.map((item, idx) => <MenuItem key={idx} value={item.id} selected = {item.id === Number(quotation.user.nationality)}>{item.name}</MenuItem>)}
          </Select>
        </Grid>
      </Grid>
    </Grid>
  );
}
   
export default Form;
