import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

import STRINGS from '../../../../strings';
import { TimeField } from '../../../../components/Fields';

import { Props } from './index';
import { MRG_BOTT } from '../types'

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: MRG_BOTT.bottomSpacing
  }
});

function PreviousJob(props: Props) {
  const classes = useStyles();
  const { quotation, setQuotation } = props;

  function onPreviousJobTime(previousJobMonths: string) {
    setQuotation({
      borrower: {
        previousJobMonths: Number(previousJobMonths) || undefined
      }
    });
  }

  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11} direction='column'>
        <Grid item xs={12} sm={6} classes={{item: classes.bottomSpacing}}>
          <TimeField
            label={STRINGS.QUOTATION.PERSONAL_LOAN.EMPLOYMENT.CHOOSE_PREVIOUS_CONTRACT_TIME}
            onChange={onPreviousJobTime}
            value={quotation.borrower.previousJobMonths}
          />
        </Grid>
      </Grid>
    </Grid>
  );
};

export default PreviousJob;