import React, { useEffect, useState } from 'react';
import parse from 'html-react-parser';
import Grid from '@material-ui/core/Grid';
import { makeStyles, Theme } from '@material-ui/core/styles';
import STRINGS from '../../../../../strings';
import { CardRadioButtons } from '../../../../../components/RadioButtons';
import { DateField, WeightField, HeightField, IdentificationField } from '../../../../../components/Fields';
import { MaleIcon, FemaleIcon } from '../../../../../components/Svg';
import TextFieldF from '../../../../../components/Fields/TextFieldF';
import Typography from '@material-ui/core/Typography';
import { Modal } from '@material-ui/core';
import { HEIGHT_UNITS, WEIGHT_UNITS } from '../../types';
import { Props } from '../index';

import Button from '@material-ui/core/Button';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from "@material-ui/core/FormControlLabel";

const { BORROWER } = STRINGS.APPLICATION.PERSONAL_LOAN;

const useStyles = makeStyles((theme: Theme) => ({
  bottomSpacing: {
    marginBottom: 40 //MRG_BOTT.bottomSpacing,
  },
  terminos: {
    marginBottom: 40,
    marginTop: 40,
    fontSize: 20
  },
  modal: {
    position:'absolute',
    top:'5%',
    left:'5%',
    width: '90%',
    background: "white",
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: "16px 32px 24px",
    transform: "tranlate(-50%, -50%)",
    // overflowY: 'scroll',
    height: '80%',
    display: 'block'
  },
  content: {
      padding: 12,
    
  },
  spacing: {
      marginBottom:56
  },
  divide: {
    width: '100%',
    height: '380px',
    textOverflow: 'ellipsis',
    overflow: 'hidden',
    overflowY: 'scroll',
    borderBottom: '2px solid #cfc9c9',
    padding: '15px',
  }
}));

const options = [{
  label: 'Masculino',
  value: 'male',
  Icon: MaleIcon
}, {
  label: 'Femenino',
  value: 'female',
  Icon: FemaleIcon
}];


function Form(props: Props) {
  const classes = useStyles()
  const { quotation, setQuotation } = props
  const { terms_cond = false } = quotation.user
  const [identify, setIdentify] = useState("")

  function onIdentificationChange(identification: string) {
    setIdentify(identification)
    window.localStorage.setItem('idPersonal', identification || '')
  }

  function onGenderSelected(gender: string): void {
    setQuotation({
      borrower: { gender },
    });
  }

  function onBirthDateSelected(birthDate?: number | null): void {
    if (birthDate) {
      setQuotation({
        borrower: { birthDate },
      });
    }
  }

  function onHeightChange(unit: HEIGHT_UNITS, height?: number): void {
    setQuotation({
      borrower: { height, heightUnit: unit },
    });
  }

  function onWeightChange(unit: WEIGHT_UNITS, weight?: number): void {
    setQuotation({
      borrower: { weight, weightUnit: unit },
    });
  }

  function onApplicationChange(key: string, value: string) {
    setQuotation({
      user: { [key]: value }})     
  }

  const handleChange = (check: boolean) => {
    setQuotation({
      user: { terms_cond: check },
    })
  };  

  const body = TermsCond(terms_cond, handleChange)

  const emailRegex = /^[-\w.%+]{1,64}@(?:[A-Z0-9-]{1,63}\.){1,125}[A-Z]{2,63}$/i
  const cellRegex = /^\d{4}(-|\s)\d{4}$/

  useEffect(() => {
    const id = window.localStorage.getItem('idPersonal') || ''
    setIdentify(id)
  }, [])


  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <IdentificationField 
            label={BORROWER.ID}
            onChange={onIdentificationChange}
            value={identify}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label="Nombre:"
            onChange={(value: string) => onApplicationChange('fname', value)}
            value={quotation.user.fname}
            error={!quotation.user.fname || quotation.user.fname.length > 20}
            message={!quotation.user.fname ? "Este es un campo Obligatorio ...": (quotation.user.fname.length > 20 ? "Máximo 20 caracteres." : "")}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label="Apellido:"
            onChange={(value: string) => onApplicationChange('lname', value)}
            value={quotation.user.lname}
            error={!quotation.user.lname || quotation.user.lname.length > 20}
            message={!quotation.user.lname ? "Este es un campo Obligatorio ...": (quotation.user.lname.length > 20 ? "Máximo 20 caracteres." : "")}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label="Email:"
            onChange={(value: string) => onApplicationChange('email', value)}
            value={quotation.user.email}
            error={!quotation.user.email || quotation.user.email.length > 60 || !emailRegex.test(quotation.user.email)}
            message={!quotation.user.email ? "Este es un campo Obligatorio ...": (quotation.user.email.length > 60 ? "Máximo 60 caracteres." : (!emailRegex.test(quotation.user.email) ? "Ej. correo@abc.com" : ""))}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label="Célular #:"
            onChange={(value: string) => onApplicationChange('cellphone', value)}
            value={quotation.user.cellphone}
            error={!quotation.user.cellphone || quotation.user.cellphone.length > 9 || !cellRegex.test(quotation.user.cellphone)}
            message={!quotation.user.cellphone ? "Este es un campo Obligatorio ...": (quotation.user.cellphone.length > 9 ? "Máximo 8 números." : (!cellRegex.test(quotation.user.cellphone) ? "Ej. 6689-2323" : ""))}
          />
        </Grid>
          <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
            <CardRadioButtons 
              label={STRINGS.QUOTATION.PERSONAL_LOAN.PROFILE.CHOOSE_GENDER}
              onClick={onGenderSelected}
              options={options}
              value={quotation.borrower.gender}
            />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <DateField 
            label={STRINGS.QUOTATION.PERSONAL_LOAN.PROFILE.CHOOSE_BIRTH}
            onChange={onBirthDateSelected}
            value={quotation.borrower.birthDate}
          />
        </Grid>
        <Grid item xs={12} sm={6} classes={{item: classes.bottomSpacing}}>
          <WeightField 
            label={STRINGS.QUOTATION.PERSONAL_LOAN.PROFILE.CHOOSE_WEIGHT}
            onChange={onWeightChange}
            value={quotation.borrower.weight}
            unit={quotation.borrower.weightUnit || WEIGHT_UNITS.POUNDS}
          />
        </Grid>
        <Grid item xs={12} sm={6} classes={{item: classes.bottomSpacing}}>
          <HeightField 
            label={STRINGS.QUOTATION.PERSONAL_LOAN.PROFILE.CHOOSE_HEIGHT}
            onChange={onHeightChange}
            value={quotation.borrower.height}
            unit={quotation.borrower.heightUnit || HEIGHT_UNITS.FEETS}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.terminos}}>
          {body}
        </Grid>
      </Grid>
    </Grid>
  );
};

function TermsCond(checked: boolean, handleChange: (check: boolean) => void) {
  
  const styles = useStyles()
  const [modal, setModal] = useState(false)

  const abrircerrarmodal = () => {
    setModal(!modal)
  }
  
  const handleChange2 = (event: React.ChangeEvent<HTMLInputElement>) => {
    handleChange(event.target.checked)
  }

  const body = (
    <>
      <div className={styles.modal}>
          <div className={styles.divide}>
            {STRINGS.TERMS.SECTIONS.map(({TITLE, CONTENT}, idx) => (
              <div key={idx} className={styles.spacing}>
                  <Typography variant='h6' gutterBottom>{TITLE}</Typography>
                  {CONTENT.map((content, idx) => 
                    <Typography key={idx} component='span' paragraph>{parse(content)}</Typography>
                  )}
              </div>
            ))}
          </div>
      
          <Grid container spacing={2} >
            <Grid item xs={12} sm={6} style={{marginTop: '20px'}}>
                <FormControlLabel
                    control={
                    <Checkbox
                        checked={checked}
                        onChange={handleChange2}
                        name="terminos"
                    />
                    }
                    label="He leído y acepto los términos y condiciones."
                />

                <Button 
                    onClick={()=>abrircerrarmodal()}
                    variant="contained" 
                    style={{marginLeft: '15px', backgroundColor: '#0C7BDD', color: '#FFFFFF'}}
                >Continuar</Button>
            </Grid>
          </Grid>
      </div>
    </>
  )

  return (
    <div>
        <Button 
            onClick={()=>abrircerrarmodal()}
            variant="contained" 
            style={{backgroundColor: '#0C7BDD', color: '#FFFFFF'}}
        >Terminos y Condiciones</Button>
        <Modal
          disableEscapeKeyDown
          open={modal}
          onClose={(event, reason) => {
            if(reason !== 'backdropClick') {
                abrircerrarmodal()
            }
          }}
        >
            {body}
        </Modal>
    </div>
  )
}

export default Form;