import React from 'react';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles } from '@material-ui/core/styles';
import { OutlinedInput } from '@material-ui/core';

const useStyles = makeStyles({
  label: {
    marginBottom: 24
  }
});

enum WEIGHT_UNITS {
  KILOGRAMS = 'kg',
  POUNDS = 'lb'
};
interface Props {
  unit: WEIGHT_UNITS;
  value?: number;
  onChange: (unit: WEIGHT_UNITS, weight?: number) => void;
  label: string;
};

const WEIGHT_OPTIONS = [{
  value: WEIGHT_UNITS.POUNDS,
  label: 'Libras'
}, {
  value: WEIGHT_UNITS.KILOGRAMS,
  label: 'Kilogramos'
}];

function WeightField(props: Props) {
  const classes = useStyles();

  function handleChange(event: React.ChangeEvent<HTMLInputElement>) {
    const weight = event.target.value as string;

    props.onChange(props.unit, Number(weight) || undefined);
  }

  function handleUnitChange(event: React.ChangeEvent<{name?: string | undefined; value: unknown; }>) {
    const toUnit = event.target.value as WEIGHT_UNITS;
  
    if (props.value) {
      switch (props.unit) {
        case WEIGHT_UNITS.POUNDS:
          if (toUnit === WEIGHT_UNITS.KILOGRAMS) {
            const weight = (props.value / 2.205).toFixed(2);
  
            props.onChange( WEIGHT_UNITS.KILOGRAMS, Number(weight));
          }

          break;
        case WEIGHT_UNITS.KILOGRAMS:
          if (toUnit === WEIGHT_UNITS.POUNDS) {
            const weight = (props.value * 2.205).toFixed(2);
  
            props.onChange(WEIGHT_UNITS.POUNDS, Number(weight));
          }
      }
    }
  }

  return (
    <Grid container>
      <Grid item xs={12} classes={{item: classes.label}}>
        <FormLabel>{props.label}</FormLabel>
      </Grid>
      <Grid item xs={12} container spacing={2}>
      <Grid container item xs={10} sm={10} spacing={2}>
          <Grid item xs={6}>
            <OutlinedInput
              type='number' 
              value={props.value} 
              onChange={handleChange} 
              placeholder='0.00' 
            />
          </Grid>
          <Grid item>
            <Select
              variant="outlined"
              value={props.unit}
              onChange={handleUnitChange}
            >
              {
                WEIGHT_OPTIONS.map((unit, idx) => {
                  return <MenuItem key={idx} value={unit.value}>{unit.label}</MenuItem>;
                })
              }
            </Select>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
};

export default WeightField;