import React, { useRef, useState, useEffect } from 'react';
import SignatureCanvas from "react-signature-canvas";

export const DigitalSignature = ( props ) => {
  const signatureRef = useRef({});
  const [ signImage, setSignImage ] = useState("");
  const [ error, setError ] = useState(true);
  // eslint-disable-next-line no-unused-vars
  const { application: { sign }, setApplication } = props;

  const saveSignature = ( signature ) => {
    setSignImage(signature);
  }

  useEffect(() => {
    setApplication({ sign: { signImage }});
  // eslint-disable-next-line react-hooks/exhaustive-deps
  },[signImage]);

  return (
    <>
      <SignatureCanvas 
        canvasProps={{
          width: 400, 
          height: 200,
          style:{"border": "1px solid #000000"}
        }}
        minWidth={2}
        maxWidth={3}
        penColor="black"
        ref={signatureRef}
        onBegin={ () => { 
          setError(false) 
        }}
      />
      <br />
      <button onClick={() => {
        signatureRef.current.clear();
        saveSignature(null);
      }}> Borrar </button>&nbsp;&nbsp;&nbsp;
      <button onClick={() => {
        saveSignature(signatureRef.current.getTrimmedCanvas().toDataURL("image/jpg"))
      }}> Listo </button>
      <pre>
      {
        error ? <h2>La firma es obligatoria</h2> : false
      }
      </pre>
    </>
  )
}