import { useState } from 'react';
import { Quotation, WEIGHT_UNITS, HEIGHT_UNITS } from '../types';

function getInitialQuotation(): Quotation {
  return {
    tracking: undefined,
    borrower: {
      jobSector: undefined,
      occupation: undefined,
      profession: undefined,
      institution: undefined,
      retirement: undefined,
      contractType: undefined,
      inicioLabores: undefined,
      mesesAntig: undefined,
      currentJobMonths: undefined,
      previousJobMonths: undefined,
      creditHistory: undefined,
      paymentFrecuency: undefined,
      wage: undefined,
      alloance: undefined,
      perDiem: undefined,
      residenceType: undefined,
      monthlyResidenceFee: undefined,
      gender: undefined,
      birthDate: undefined,
      weight: undefined,
      weightUnit: WEIGHT_UNITS.POUNDS,
      height: undefined,
      heightUnit: HEIGHT_UNITS.FEETS,
    },
    loan: {
      // entity_f: undefined,
      bank: undefined,
      amount: undefined,
      cashOnHand: undefined,
      monthlyFee: undefined,
      term: undefined,
      reason: undefined,
      opciones: undefined,
      causasRechazo: undefined,
    },
    user: {
      email: undefined,
      name: undefined,
      fname: undefined,
      lname: undefined,
      picture: undefined,
      origin: undefined,
      fname_2: undefined,
      lname_2: undefined,
      cellphone: undefined,
      nationality: undefined,
      terms_cond: undefined,
    },
    finance: {
      entity: undefined
    }
  }
}

function useQuotation(): [Quotation, (quotation: Partial<Quotation>) => void] {
  const [quotation, setQuotation] = useState(getInitialQuotation());

  function saveQuote(quotation: Partial<Quotation>): void {
    const {
      borrower,
      loan,
      user,
      finance,
      tracking
    } = quotation;

    setQuotation((prevApplication) => ({
      borrower: {
        ...prevApplication.borrower,
        ...borrower,
      },
      loan: {
        ...prevApplication.loan,
        ...loan,
      },
      user: {
        ...prevApplication.user,
        ...user
      },
      finance: {
        ...prevApplication.finance,
        ...finance
      },
      tracking
    }));
  }

  return [quotation, saveQuote];
}

export default useQuotation;