import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import PrivateForm from './PrivateForm';
import PublicForm from './PublicForm';
import RetiredForm from './RetiredForm';
import { Props } from '../index';
import { JOB_SECTORS } from '../../types';

export interface Institution {
  id: number;
  name: string;
  subgrupo: number;
}

export interface Profession {
  id: number;
  name: string;
  type?: number;
};

export interface LaboralSector {
  id: number;
  sector: string;
  name: string;
}

function Occupation(props: Props) {
  const jSector = localStorage.getItem("jSector") || undefined
  let sector: JOB_SECTORS = jSector as JOB_SECTORS

  const {
    data: laboralSectorResponse,
    loading: laboralSectorLoading,
  } = useFetch<LaboralSector[]>('/laboral_sector', { method: 'GET' }, []);

  const { 
    data: institutionsResponse, 
    loading: institutionsLoading 
  } = useFetch<Institution[]>('/laboral_sector_institution', { method: 'GET' }, []);

  const { 
    data: profesions_acpResponse, 
    loading: profesions_acpLoading 
  } = useFetch<Profession[]>('/profesions_acp', { method: 'GET' }, []);

  const { 
    data: profesions_lwResponse, 
    loading: profesions_lwLoading 
  } = useFetch<Profession[]>('/profesions_lw', { method: 'GET' }, []);

  const { 
    data: planillasResponse, 
    loading: planillasLoading 
  } = useFetch<Profession[]>('/planillas_j', { method: 'GET' }, []);

  const { 
    data: ranges_polResponse, 
    loading: ranges_polLoading 
  } = useFetch<Profession[]>('/ranges_pol', { method: 'GET' }, []);

  const loading = laboralSectorLoading || institutionsLoading || profesions_acpLoading || profesions_lwLoading || planillasLoading || ranges_polLoading; 

  if (loading || !institutionsResponse || !laboralSectorResponse || !profesions_acpResponse || !profesions_lwResponse || !planillasResponse || !ranges_polResponse) { 

    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  const laboralSectors = laboralSectorResponse?.filter((
          laboralSector) => laboralSector.sector === sector);

  switch (sector) {
    case JOB_SECTORS.PRIVATE:
      return (
        <PrivateForm
          {...props}
          laboralSectors={laboralSectors}
          professions={profesions_lwResponse}
        />
      );
    case JOB_SECTORS.PUBLIC:
      return (
        <PublicForm
          {...props}
          laboralSectors={laboralSectors}
          institutions={institutionsResponse}
          professions_lw={profesions_lwResponse}
          professions_acp={profesions_acpResponse}
          ranges_pol={ranges_polResponse}
        />
      );
    case JOB_SECTORS.RETIRED:
      return (
        <RetiredForm
          {...props}
          laboralSectors={laboralSectors}
          planillas={planillasResponse}
        />
      );
    default:
      return null;
  }
};

export default Occupation;