import React from 'react';
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import CloseIcon from '@material-ui/icons/Close';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
// import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { makeStyles, Theme, useTheme } from '@material-ui/core/styles';
import PRODUCTS from '../../../lib/products';

import NavbarButton from './NavbarButton';
import NavbarDropdown from './NavbarDropdown';
import { FinanservsHorizontalLogoImg } from '../../Images';
import { PanamanianFlagIcon } from '../../Svg';

const useStyles = makeStyles((theme: Theme) => ({
  logo: {
    width: 192,
    cursor: 'pointer',
    [theme.breakpoints.down('md')]: {
      width: 172,
      cursor: 'pointer',
      marginRight: 16,
    }
  },
  mobile: {
    color: theme.palette.common.white
  },
  button: {
    color: theme.palette.common.black,
    '&:hover': {
      color: theme.palette.primary.dark,
    }
  },
  logIn: {
    color: theme.palette.success.main,
    '&:hover': {
      color: theme.palette.success.dark,
    }
  }
}));

interface Props {
  open: boolean;
  hasScrolled?: boolean;
  dropdownOptions?: Array<typeof PRODUCTS['PERSONAL_LOAN']>;
  onDropdownOptionClick?: (path: string) => void;
  onLogoClick: () => void;
  onAnchorClick?: (elementId: string) => void;
  onLogInClick?: () => void;
  onMenuClick: () => void;
};

function Navbar(props: Props) {
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const { open, hasScrolled = true, dropdownOptions, onDropdownOptionClick, onAnchorClick, onLogInClick, onMenuClick } = props;
  const theme = useTheme();
  const desktop = useMediaQuery(theme.breakpoints.up('md'));
  const classes = useStyles();

  function renderLogo() {
    // if (!hasScrolled) {  // LLRR: 2021-11-08
    //   return <FinanservsHorizontalLogoWhiteImg className={classes.logo} onClick={props.onLogoClick} />
    // }

    return <FinanservsHorizontalLogoImg className={classes.logo} onClick={props.onLogoClick} />;
  }

  function renderMobileMenu() {
    return (
      <Grid item classes={{item: classes.mobile}}>
        <IconButton color={hasScrolled ? 'default': 'inherit'} onClick={onMenuClick}>
          { open ? <CloseIcon /> : <MenuIcon /> }
        </IconButton>
      </Grid>
    );
  }

  // Comentado 2021-11-07 LLRR
  function renderLoginbutton() {
    return (
      <Grid item classes={{item: classes.logIn}}>
        {/* <NavbarButton
          hasScrolled={hasScrolled}
          onClick={onLogInClick}
          startIcon={<LockOutlinedIcon fontSize='small' color='inherit' />}
        >
          Iniciar sesión
        </NavbarButton> */}
      </Grid>
    );
  }
  
  function renderNavbarButtons() {
    return (
      <Grid container item xs={5} alignItems='center' justify='space-between'>
        <Grid item classes={{item: classes.button}}>
          <NavbarDropdown
            hasScrolled={hasScrolled}
            options={dropdownOptions}
            onClick={onDropdownOptionClick}
            endIcon={<KeyboardArrowDownIcon fontSize='small' />}
          >
            Compara
          </NavbarDropdown>
        </Grid>
        <Grid item classes={{item: classes.button}}>
          <NavbarButton hasScrolled={hasScrolled} onClick={() => handleAnchorClick('#steps')}>¿Cómo empezar?</NavbarButton>
        </Grid>
        <Grid item classes={{item: classes.button}}>
          <NavbarButton hasScrolled={hasScrolled} onClick={() => handleAnchorClick('#faqs')}>Preguntas frecuentes</NavbarButton>
        </Grid>
      </Grid>
    );
  }

  function handleAnchorClick(elementId: string) {
    if (onAnchorClick) {
      onAnchorClick(elementId)
    }
  }

  return (
    <Grid container alignItems='center' justify='space-between'>
      <Grid item>
        {renderLogo()}
      </Grid>
      { desktop && renderNavbarButtons() }
      <Grid item container xs={3} sm={2} alignItems='center' justify='space-between'>
        { desktop && renderLoginbutton() }
        { desktop &&  <PanamanianFlagIcon fontSize='large' /> }
        { !desktop && renderMobileMenu() }
      </Grid>
    </Grid>
  );
};

export default Navbar;
