import React, { useEffect } from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useLocation
} from 'react-router-dom';

// Components
import { PublicNavbar, ApplicationNavbar } from './components/Navbar';
import { Footer, FooterLite } from './components/Footer';

// Views
import Home from './views/Home';
import Terms from './views/Terms';
import Tracking from './views/Tracking';
import Aproach from './views/Tracking/Aproach'
import Quotation from './views/Quotation';
import Application from './views/Application';
import { SectorProvider } from "./context/sector/Provider";

function App() {

  return (
    <SectorProvider>
    <Router>
      <ScrollToTop />
      <div style={{overflow:'hidden'}}>
        <Switch>

          <Route path="/cotizar">
            <div style={{display:'flex', height:'100vh', flexDirection:'column', overflow:'auto'}}>
              <ApplicationNavbar />
              <div style={{flex:1}}>
                <Quotation />
              </div>
              <FooterLite />
            </div>
          </Route>


          {/* /solicitud/prestamo-personal */}
          <Route path="/solicitud">  
            <div style={{display:'flex', height:'100vh', flexDirection:'column', overflow:'auto'}}>
              <ApplicationNavbar />
              <div style={{flex:1}}>
                <Application />
              </div>
              <FooterLite />
            </div>
          </Route>

          {/*<Route path="/terminos-condiciones">*/}
          <Route path="/politica-de-privacidad">
            <PublicNavbar />
            <Terms />
            <FooterLite />
          </Route>

          <Route path="/seguimiento">
            <Tracking />
            <Aproach />
            <Footer />
          </Route>          

          <Route path="/">
            <PublicNavbar />
            <Home />
            <Footer />
          </Route>

        </Switch>
      </div>
    </Router>
    </SectorProvider>
  );
}

function ScrollToTop() {
  let { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
}

export default App;
