import React from 'react';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles } from '@material-ui/core/styles';
import { OutlinedInput } from '@material-ui/core';

const useStyles = makeStyles({
  label: {
    marginBottom: 24
  }
});

enum HEIGHT_UNITS {
  METERS = 'mts',
  FEETS = 'ft'
};

interface Props {
  unit: HEIGHT_UNITS;
  value?: number;
  onChange: (unit: HEIGHT_UNITS, height?: number) => void;
  label: string;
};

const HEIGHT_OPTIONS = [{
  value: HEIGHT_UNITS.METERS,
  label: 'Metros'
}, {
  value: HEIGHT_UNITS.FEETS,
  label: 'Pies'
}];

function HeightField(props: Props) {
  const classes = useStyles();

  function handleChange(event: React.ChangeEvent<HTMLInputElement>) {
    const height = event.target.value as string;

    props.onChange(props.unit, Number(height) || undefined);
  }

  function handleUnitChange(event: React.ChangeEvent<{name?: string | undefined; value: unknown; }>) {
    const toUnit = event.target.value as HEIGHT_UNITS;
  
    if (props.value) {
      switch (props.unit) {
        case HEIGHT_UNITS.METERS:
          if (toUnit === HEIGHT_UNITS.FEETS) {
            const height = (props.value * 3.281).toFixed(2);
  
            props.onChange(HEIGHT_UNITS.FEETS, Number(height));
          }

          break;
        case HEIGHT_UNITS.FEETS:
          if (toUnit === HEIGHT_UNITS.METERS) {
            const height = (props.value / 3.281).toFixed(2);
  
            props.onChange(HEIGHT_UNITS.METERS, Number(height));
          }
      }
    }
  }

  return (
    <Grid container>
      <Grid item xs={12} classes={{item: classes.label}}>
        <FormLabel>{props.label}</FormLabel>
      </Grid>
      <Grid item xs={12} container spacing={2}>
      <Grid container item xs={10} sm={10} spacing={2}>
          <Grid item xs={6}>
            <OutlinedInput
              type='number' 
              value={props.value} 
              onChange={handleChange} 
              placeholder='0.00' 
            />
          </Grid>
          <Grid item>
            <Select 
              variant="outlined"
              value={props.unit}
              onChange={handleUnitChange}
            >
              {
                HEIGHT_OPTIONS.map((unit, idx) => {
                  return <MenuItem key={idx} value={unit.value}>{unit.label}</MenuItem>;
                })
              }
            </Select>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
};

export default HeightField;