import React, { useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import { makeStyles, Theme } from '@material-ui/core/styles';
import CloudUploadOutlinedIcon from '@material-ui/icons/CloudUploadOutlined';
import DescriptionOutlinedIcon from '@material-ui/icons/DescriptionOutlined';
import { SvgIconProps } from '@material-ui/core/SvgIcon';
import { BacLogoImg, MultibankLogoImg, StGeorgeLogoImg, GlobalBankLogoImg } from '../../../components/Images';
import Stepper from '../../../components/Stepper';
import STRINGS from '../../../strings';
import useApplication from './hooks/useApplication';
import useStep from './hooks/useStep';
import { STEPS } from './types';

const useStyles = makeStyles((theme: Theme) => ({
  container: {
    display: 'flex', 
    flexDirection: 'column', 
    height: '100%', 
    paddingTop: 72, 
    paddingBottom: 72,
    [theme.breakpoints.down('sm')]: {
      paddingTop: 24, 
      paddingBottom: 24
    }
  },
  title: {
    marginBottom: 50
  }
}));

function PersonalLoan() {
  const [application, setApplication] = useApplication();
  const { step, next, prev, disabled, Form } = useStep(application, setApplication);
  const classes = useStyles();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [step]);

  return (
    <Container classes={{root: classes.container}}>
      <Header 
        title={STRINGS.APPLICATION.PERSONAL_LOAN[step].TITLE}
        subtitle={step !== STEPS.DOCUMENTS && STRINGS.APPLICATION.PERSONAL_LOAN[step].SUBTITLE}
        Icon={getStepIcons(step)}
      />
      <div style={{flex:1, marginBottom: 56}}>
        {Form}
      </div>
      <Stepper onNext={next} onPrev={prev} disabled={disabled} />
    </Container>
  );
};

function getStepIcons(step: STEPS): (props: SvgIconProps) => React.ReactElement<SvgIconProps> {
  switch (step) {
    case STEPS.DOCUMENTS:
      return (props: SvgIconProps) => <CloudUploadOutlinedIcon {...props} />;
    default:
      return (props: SvgIconProps) => <DescriptionOutlinedIcon {...props} />;
  }
}

interface HeaderProps {
  title: string;
  subtitle: string | boolean;
  Icon: (props: SvgIconProps) => React.ReactElement<SvgIconProps>;
}

function Header(props: HeaderProps) {
  const { Icon, title, subtitle } = props;
  const classes = useStyles();

  let bank = ''
  const data = window.localStorage.getItem('quotation');
  if (data) {
    const dataObj = JSON.parse(data)
    bank = dataObj.loan
  }

  return (
    <Grid container justify='space-between' classes={{container: classes.title}}>
      <Grid item>
        <Grid container>
          <Grid item>
            <Icon color='primary' fontSize='large' style={{ marginRight: 16 }} />
          </Grid>
          <Grid item>
            <Typography variant='h5'>{title}</Typography>
            {subtitle && (
              <Typography color='textSecondary' variant='subtitle1'>
                {subtitle}
              </Typography>
            )}
          </Grid>
        </Grid>
      </Grid>
      <Grid item>
        { bank === 'bac' ? <BacLogoImg width={120} /> : '' }
        { bank === 'st_george' ? <StGeorgeLogoImg width={120} /> : '' }
        { bank === 'multibank' ? <MultibankLogoImg width={120} /> : '' }
        { bank === 'global_bank' ? <GlobalBankLogoImg width={120} /> : '' }
      </Grid>
    </Grid>
  );
}

export default PersonalLoan;