import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';

import Form from './Form';

export interface Purpose {
  name: string;
  is_active: boolean;
}

function Reason(props: Props) {
  const { data: loanPurposes, loading } = useFetch<Purpose[]>('/purpose', { method: 'GET' }, []);
  
  if (loading || !loanPurposes) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form {...props} loanPurposes={loanPurposes} />;
};

export default Reason;