import React from 'react';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import Typography from '@material-ui/core/Typography';
import { makeStyles, Theme } from '@material-ui/core/styles';

const useStyles = makeStyles((theme: Theme) => ({
  label: {
    marginBottom: 36
  },
  pill: {
    padding: 16,
    width: '100%',
    background: theme.palette.grey['200'],
    textTransform: 'uppercase',
    textAlign: 'center',
    borderRadius: 48,
    cursor: 'pointer'
  },
  selected: {
    background: theme.palette.primary.main,
    color: theme.palette.common.white
  }
}));

interface RadioOption {
  label: string;
  value: any;
};

interface Props<T> {
  label: string;
  onClick: (value: T) => void;
  options: RadioOption[];
  value?: T;
  helper?: string;
};

function CardRadioButtons<T>(props: Props<T>) {
  const classes = useStyles();
  return (
    <Grid container>
      <Grid item xs={12} classes={{item: classes.label}}>
        <FormLabel>{props.label}</FormLabel>
        {props.helper && <Typography variant='body2' color='textSecondary'>{props.helper}</Typography>}
      </Grid>
      <Grid item xs={12} container spacing={2}>
        {props.options.length && props.options.map((option, idx) => (
          <Grid key={idx} item xs={12} sm={3}>
            <Typography 
              onClick={() => props.onClick(option.value)}
              variant='body2'
              classes={{root: `${classes.pill} ${option.value === props.value && classes.selected}`}}
            >
              {option.label}
            </Typography>
          </Grid>
        ))}
      </Grid>
    </Grid>
  );
}

export default CardRadioButtons;