import React from 'react';
import { Props } from '../index';

import Form from './Form';

export interface Loan {
  bank: string;
  loan: number;
  term: number;
  paysYear: number;
  monthlyFee: number;
  cashOnHand: number;
}

function Banks(props: Props) {

  return <Form 
    {...props} 
  />
};

export default Banks;