import React from 'react';
import Grid from '@material-ui/core/Grid';
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import WorkOutlineOutlinedIcon from '@material-ui/icons/WorkOutlineOutlined';
import AccountBalanceOutlinedIcon from '@material-ui/icons/AccountBalanceOutlined';
import SchoolOutlinedIcon from '@material-ui/icons/SchoolOutlined';
import { SvgIconProps } from '@material-ui/core/SvgIcon';
import { makeStyles } from '@material-ui/core/styles';

import STRINGS from '../../../../../strings';
import { CardRadioButtons } from '../../../../../components/RadioButtons';

import { PRIVATE_OCCUPATION } from '../../types';
import { Props as BorrowerProps } from '../index';
import { LaboralSector, Profession } from './index';

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: 96
  }
});

interface AutocompleteOption {
  id: string;
  name: string;
};

function getOccupationIcon(): Record<string, React.ElementType<SvgIconProps>> {
  return ({
    [PRIVATE_OCCUPATION.EDUCATION]: SchoolOutlinedIcon,
    [PRIVATE_OCCUPATION.HEALTHCARE]: AccountBalanceOutlinedIcon,
    [PRIVATE_OCCUPATION.OTHER]: WorkOutlineOutlinedIcon
  });
};

interface Props extends BorrowerProps {
  laboralSectors: LaboralSector[];
  professions: Profession[];
};

function PrivateForm(props: Props) {
  const classes = useStyles();
  const { quotation: { borrower }, setQuotation, laboralSectors, professions } = props;

  function onProfessionSelected(profession: string) {
    console.log('SSSS',profession)
    const sector = laboralSectors.filter(sector => sector.id === parseInt(profession))
    console.log('DDDD',sector)

    setQuotation({ 
      borrower: { 
        profession, 
        occupation: undefined 
      }
    });
  }

  function onOccupationSelected(
    event: React.ChangeEvent<{}>,
    option: AutocompleteOption | null,
    reason: string
  ) {
    if (option) {
      let typeProf = 0
      for (let p of professions) {
        if(p.id === Number(option.id)) {
          typeProf = p.type || 0
          window.localStorage.setItem('typeProf', typeProf.toString())
          break;
        }
      };
  
      setQuotation({ borrower: { occupation: option.id } });
    }
  }
  
  const options = laboralSectors.map((laboralSector) => ({
    label: laboralSector.name,
    value: String(laboralSector.id),
    Icon: getOccupationIcon()[String(laboralSector.id)]
  }));
  
  const healthcare = laboralSectors.find((laboralSector) => String(laboralSector.id) === PRIVATE_OCCUPATION.HEALTHCARE);
  // const professions = healthcare ? healthcare.professions : [];
  const professions1 = healthcare ? professions : [];

  const healthcareOptions = professions1.map((profession) => ({
    id: String(profession.id),
    name: profession.name
  }));

  const occupation = healthcareOptions.find((option) => (
    option.id === borrower.occupation
  ));

  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <CardRadioButtons 
            label={STRINGS.QUOTATION.PERSONAL_LOAN.OCCUPATION.CHOOSE_PROFESSION}
            onClick={onProfessionSelected}
            options={options}
            value={borrower.profession}
          />
        </Grid>
        {borrower.profession === PRIVATE_OCCUPATION.HEALTHCARE && (
          <Grid item xs={12} sm={6} classes={{item: classes.bottomSpacing}}>
            <Autocomplete
              value={occupation}
              onChange={onOccupationSelected}
              options={healthcareOptions}
              getOptionLabel={(option: AutocompleteOption) => option.name}
              getOptionSelected={(option: AutocompleteOption, value: AutocompleteOption) => option.id === value.id}
              renderInput={(params) => <TextField {...params} label={STRINGS.QUOTATION.PERSONAL_LOAN.OCCUPATION.CHOOSE_OCCUPATION} />}
            />
          </Grid>
        )}
      </Grid>
    </Grid>
  );
};

export default PrivateForm;