import React from 'react';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import { makeStyles } from '@material-ui/core/styles';

import STRINGS from '../../../../../strings';
import { PillRadioButtons } from '../../../../../components/RadioButtons';
import { CurrencyField } from '../../../../../components/Fields';
import { RESIDENCE_TYPES } from '../../types';
import { Props as QuotationProps } from '../index';
import { ResidenceType } from './index';
import { MRG_BOTT } from '../../types'

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: MRG_BOTT.bottomSpacing
  },
  label: {
    marginBottom: MRG_BOTT.label
  }
});

interface Props extends QuotationProps {
  residenceTypes: ResidenceType[]
};

function Form(props: Props) {
  const classes = useStyles();
  const { quotation: { borrower }, setQuotation, residenceTypes } = props;
  
  function onResidenceTypeSelected(residenceType: RESIDENCE_TYPES) {
    setQuotation({
      borrower: {
        residenceType,
        monthlyResidenceFee: undefined
      }
    });
  }

  function onMonthlyResidenceFee(monthlyResidenceFee: string) {
    setQuotation({
      borrower: {
        monthlyResidenceFee: Number(monthlyResidenceFee) || undefined,
      },
    });
  }

  const options = residenceTypes?.map((residenceType) => ({
    label: residenceType.name,
    value: String(residenceType.id)
  }));

  const monthlyResidenceFeeRequired = isMonthlyResidenceFeeRequired(borrower.residenceType);
  const monthlyResidenceLabel = borrower.residenceType === RESIDENCE_TYPES.RENT ? STRINGS.QUOTATION.PERSONAL_LOAN.RESIDENCE.CHOOSE_RENT : STRINGS.QUOTATION.PERSONAL_LOAN.RESIDENCE.CHOOSE_MORTGAGE;

  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <PillRadioButtons
            label={STRINGS.QUOTATION.PERSONAL_LOAN.RESIDENCE.CHOOSE_RESIDENCE_TYPE}
            onClick={onResidenceTypeSelected}
            options={options}
            value={borrower.residenceType}
          />
        </Grid>
        {monthlyResidenceFeeRequired && (
          <>
            <Grid item xs={12} classes={{item: classes.label}}>
              <FormLabel>{monthlyResidenceLabel}</FormLabel>
            </Grid>
            <Grid item xs={12} sm={6}>
              <CurrencyField
                label={'Mensuales'}
                onChange={onMonthlyResidenceFee}
                value={borrower.monthlyResidenceFee}
                endLabel={true}
              />
            </Grid>
          </>
        )}
      </Grid>
    </Grid>
  );
};

function isMonthlyResidenceFeeRequired(residenceType?: RESIDENCE_TYPES): boolean {
  return !!residenceType && [RESIDENCE_TYPES.RENT, RESIDENCE_TYPES.MORTGAGE].includes(residenceType);
}

export default Form;