import React from 'react';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import { SvgIconProps } from '@material-ui/core/SvgIcon';
import Typography from '@material-ui/core/Typography';
import FlagOutlinedIcon from '@material-ui/icons/FlagOutlined';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import FlagIcon from '@material-ui/icons/Flag';
import { makeStyles, Theme, useTheme } from '@material-ui/core/styles';

import { FirstStepImg, SecondStepImg, ThirdStepImg } from '../../components/Images';

import STRINGS from '../../strings';

const useStyles = makeStyles((theme: Theme) => ({
  container: {
    paddingTop: 72,
    paddingBottom: 72
  },
  background: {
    backgroundColor: theme.palette.primary.light
  },
  stepsSubtitle: {
    fontWeight: theme.typography.fontWeightRegular
  },
  stepTitle: {
    fontWeight: theme.typography.fontWeightMedium
  },
  stepSubtitle: {
    fontWeight: theme.typography.fontWeightRegular
  },
  stepImg: {
    width: '100%'
  },
  flagIcon: {
    color: theme.palette.success.dark
  }
}));

const steps = [{
  icon: FlagOutlinedIcon,
  image: FirstStepImg,
  title: STRINGS.HOME.STEPS.STEP_ONE.TITLE,
  subtitle: STRINGS.HOME.STEPS.STEP_ONE.SUBTITLE
}, {
  icon: FlagOutlinedIcon,
  image: SecondStepImg,
  title: STRINGS.HOME.STEPS.STEP_TWO.TITLE,
  subtitle: STRINGS.HOME.STEPS.STEP_TWO.SUBTITLE
}, {
  icon: FlagIcon,
  image: ThirdStepImg,
  title: STRINGS.HOME.STEPS.STEP_THREE.TITLE,
  subtitle: STRINGS.HOME.STEPS.STEP_THREE.SUBTITLE
}];

interface StepWrapperProps {
  position: number;
  children: React.ReactChild[];
};

function StepWrapper({ position, children }: StepWrapperProps) {
  const classes = useStyles();

  const DefaultLayout = () => (
    <Container classes={{ root: classes.container }}>
      <Grid container justify='center'>
        <Grid container item xs={12} sm={10} justify='space-between' alignItems='center' spacing={10}>
          {children}
        </Grid>
      </Grid>
    </Container>
  );

  if (position % 2) {
    return (
      <div className={classes.background}>
        <DefaultLayout /> 
      </div>
    );
  }
  
  return <DefaultLayout />;
}


interface StepTextProps {
  Icon: React.FC<SvgIconProps>;
  title: string;
  subtitle: string;
};

function StepText({Icon, title, subtitle}: StepTextProps) {
  const classes = useStyles();

  return (
    <Grid item xs={12} sm={6}>
      <Icon fontSize='large' classes={{root: classes.flagIcon}} />
      <Typography variant='h5' classes={{root: classes.stepTitle }} gutterBottom>{title}</Typography>
      <Typography variant='h6' classes={{root: classes.stepSubtitle}}>{subtitle}</Typography>
    </Grid>
  );
}

interface StepImageProps {
  Image: React.FC<React.ImgHTMLAttributes<HTMLImageElement>>;
};

function StepImage({Image}: StepImageProps) {
  const classes = useStyles();

  return (
    <Grid item xs={12} sm={6}>
      <Image className={classes.stepImg} />
    </Grid>
  );
}

function Steps() {
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.up('sm'));

  return (
    <>
      {steps.map((step, idx) => {
        if (idx % 2 && matches) {
          return (
            <StepWrapper key={idx} position={idx}>
              <StepImage Image={step.image}/>
              <StepText Icon={step.icon} title={step.title} subtitle={step.subtitle} />
            </StepWrapper>
          );
        }
    
        return (
          <StepWrapper key={idx} position={idx}>
            <StepText Icon={step.icon} title={step.title} subtitle={step.subtitle} />
            <StepImage Image={step.image}/>
          </StepWrapper>
        );
      })}
    </>
  );
}

export default Steps;