import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import { SvgIconProps } from '@material-ui/core/SvgIcon';
import { makeStyles, Theme } from '@material-ui/core/styles';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    cursor: 'pointer',
    height: '100%'
  },
  content: {
    textAlign: 'center',
    padding: 24
  },
  icon: {
    width: 58,
    height: 58
  },
  title: {
    textTransform: 'uppercase'
  },
  selected: {
    backgroundColor: theme.palette.primary.main,
    color: theme.palette.common.white
  }
}));

interface Props {
  title: string;
  Icon?: React.ElementType<SvgIconProps>;
  selected?: boolean;
  onClick: () => void;
};

function OptionCard(props: Props) {
  const classes = useStyles();
  const { Icon, selected } = props;
  
  return (
    <Card 
      variant='outlined' 
      onClick={props.onClick} 
      classes={{root: `${classes.root} ${selected && classes.selected}`}}
      data-cy='option-card-radio-button'
    >
      <CardContent classes={{root: classes.content}}>
        {Icon && <Icon color={selected ? 'inherit' : 'primary'} classes={{root: classes.icon}} />}
        <Typography variant='body2' align='center' color={selected ? 'inherit' : 'textPrimary'} classes={{root: classes.title}}>
          {props.title}
        </Typography>
      </CardContent>
    </Card>
  );
}

export default OptionCard;