import React from 'react';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles } from '@material-ui/core/styles';

import STRINGS from '../../strings';

const useStyles = makeStyles({
  label: {
    marginBottom: 24
  }
});

interface Option {
  id: string;
  name: string;
};

interface Props {
  label: string;
  onChange: (value: string) => void;
  options: Option[];
  value?: number;
};

function SelectTimeField(props: Props) {
  const classes = useStyles();

  function onMonthsChange(event: React.ChangeEvent<{ value: unknown }>) {
    props.onChange(event.target.value as string);
  };

  return (
    <Grid container>
      <Grid item xs={12} classes={{item: classes.label}}>
        <FormLabel>{props.label}</FormLabel>
      </Grid>
      <Grid item xs={12} container spacing={2}>
        <Grid container item xs={8} sm={6} spacing={2}>
          <Grid item xs={6}>
            <Select fullWidth value={props.value} onChange={onMonthsChange}>
              {props.options.map((option, idx) => <MenuItem key={idx} value={option.id}>{option.name}</MenuItem>)}
            </Select>
          </Grid>
          <Grid item>
            <FormLabel>{STRINGS.FIELDS.TIME.MONTHS}</FormLabel>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
}

export default SelectTimeField;