import React from 'react';
import Grid from '@material-ui/core/Grid';
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import DirectionsBoatOutlinedIcon from '@material-ui/icons/DirectionsBoatOutlined';
import SecurityOutlinedIcon from '@material-ui/icons/SecurityOutlined';
import SchoolOutlinedIcon from '@material-ui/icons/SchoolOutlined';
import { SvgIconProps } from '@material-ui/core/SvgIcon';
import { makeStyles } from '@material-ui/core/styles';

import STRINGS from '../../../../../strings';
import { CardRadioButtons } from '../../../../../components/RadioButtons';
import { CalculatorIcon, StethoscopeIcon } from '../../../../../components/Svg';
import { Props as BorrowerProps } from '../index';
import { LaboralSector, Institution, Profession } from './index';

import { PUBLIC_OCCUPATION } from '../../types';

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: 96
  }
});

interface Props extends BorrowerProps {
  institutions: Institution[];
  laboralSectors: LaboralSector[];
  professions_acp: Profession[];
  professions_lw: Profession[];
  ranges_pol: Profession[];
};

function getOccupationIcon(): Record<string, React.ElementType<SvgIconProps>> {
  return ({
    [PUBLIC_OCCUPATION.HEALTHCARE]: StethoscopeIcon,
    [PUBLIC_OCCUPATION.EDUCATION]: SchoolOutlinedIcon,
    [PUBLIC_OCCUPATION.ADMINISTRATION]: CalculatorIcon,
    [PUBLIC_OCCUPATION.ACP]: DirectionsBoatOutlinedIcon,
    [PUBLIC_OCCUPATION.SECURITY]: SecurityOutlinedIcon
  });
};

function PublicForm(props: Props) {
  const classes = useStyles();
  const { quotation: { borrower }, setQuotation, laboralSectors, institutions, professions_acp, professions_lw, ranges_pol } = props;

  // let occup_inst = 1

  function onProfessionSelected(profession: string) {
    setQuotation({
      borrower: {
        profession, 
        occupation: undefined,
        institution: undefined
      }
    });
  }
  
  function onOccupationSelected(event: React.ChangeEvent<{}>, option: AutocompleteOption | null, reason: string) {
    if (option) {
        let typeProf = 0
        for (let p of professions_lw) {
          if(p.id === Number(option.id)) {
            typeProf = p.type || 0
            window.localStorage.setItem('typeProf', typeProf.toString())
            break;
          }
        };

        setQuotation({
          borrower: { occupation: option.id, institution: "0" },
        });
      // }
    }
  }

  function renderAutocomplete(institutionRequired: boolean) {
    let professions: any = [];
    if (borrower.profession === "5") {
      professions = professions_acp;
    } else if (borrower.profession === "6") {
      // occup_inst = 3
      professions = ranges_pol;
    } else {
      professions = professions_lw;
    }

    // if(institutionRequired) occup_inst = 2

    const options = toAutocompleteProps(institutionRequired ? institutions : professions);

    const value = options.find((option) => 
    (option.id === borrower.occupation || option.id === borrower.institution));

    const label = institutionRequired ?
      STRINGS.QUOTATION.PERSONAL_LOAN.OCCUPATION.CHOOSE_INSTITUTION :
      (rangeRequired ? STRINGS.QUOTATION.PERSONAL_LOAN.OCCUPATION.CHOOSE_RANGE : 
        STRINGS.QUOTATION.PERSONAL_LOAN.OCCUPATION.CHOOSE_OCCUPATION)

    return (
      <Grid item xs={12} sm={6} classes={{item: classes.bottomSpacing}}>
        <Autocomplete
          onChange={onOccupationSelected}
          options={options}
          value={value}
          getOptionLabel={(   option: AutocompleteOption) => option.name}
          getOptionSelected={(option: AutocompleteOption, 
                               value: AutocompleteOption) => option.id === value.id}
          renderInput={(params) => <TextField {...params} label={label} />}
        />
      </Grid>
    );
  }
  // FIN function renderAutocomplete()

  const options = laboralSectors.map((laboralSector) => ({
    label: laboralSector.name,
    value: String(laboralSector.id),
    Icon: getOccupationIcon()[String(laboralSector.id)]
  }));

  let occupationRequired = (
    PUBLIC_OCCUPATION.HEALTHCARE === borrower.profession ||
    PUBLIC_OCCUPATION.ACP === borrower.profession
  );
  let institutionRequired = (PUBLIC_OCCUPATION.ADMINISTRATION === borrower.profession);

  let rangeRequired = (PUBLIC_OCCUPATION.SECURITY === borrower.profession);

  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <CardRadioButtons 
            label={STRINGS.QUOTATION.PERSONAL_LOAN.OCCUPATION.CHOOSE_PROFESSION}
            onClick={onProfessionSelected}
            options={options}
            value={borrower.profession}
          />
        </Grid>
        {(occupationRequired || institutionRequired || rangeRequired) && renderAutocomplete(institutionRequired)}
      </Grid>
    </Grid>
  );
};

interface AutocompleteOption {
  id: string;
  name: string;
};

function toAutocompleteProps(options: Array<Profession | Institution>): AutocompleteOption[] {
  return options.map((option) => ({
    id: String(option.id),
    name: option.name
  }));
}

export default PublicForm;