import React from 'react';
import { makeStyles,  } from '@material-ui/core/styles';
import { Props as ApplicationProps } from '../index';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Checkbox from '@material-ui/core/Checkbox';

import IconButton from '@material-ui/core/IconButton';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import EventBusyIcon from '@material-ui/icons/EventBusy';
import SportsHandballIcon from '@material-ui/icons/SportsHandball';
import LocalAtmIcon from '@material-ui/icons/LocalAtm';

import CreditCardIcon from '@material-ui/icons/CreditCard';
import DriveEtaIcon from '@material-ui/icons/DriveEta';
import HouseIcon from '@material-ui/icons/House';
import AuthApcPdf from '../../../../../components/AuthApcPdf';
import ReferApcPdf from '../../../../../components/ReferApcPdf';
import { iApc, IParamsCalcLoan, LaboralEntity } from '../../../../../interfaces';
import { LaboralSector } from './index';



const useStyles = makeStyles((theme) => ({
  table: {
    width: "100%",
    overflowX: 'auto',
    
  },
  root: {
    flexGrow: 1
  },
  bottomRight: {
    justifyContent: "flex-end"
  },
  outerColumn: {
    borderRight: "1px solid grey",
    borderBottom: "1px solid grey",
    borderLeft: "1px solid grey"
  },
  centerColumn: {
    borderBottom: "1px solid grey"
  },
  margin: {
    margin: theme.spacing(1),
  },
}));

interface Props extends ApplicationProps {
  refApc: iApc[];
  laboralEntitys: LaboralEntity[];
  sectors: LaboralSector[];
}


function Form(props: Props) {
  const classes = useStyles();
  const { application: { borrower }, setApplication, refApc, laboralEntitys, sectors } = props;

  const [loansActive, setLoansActive] = React.useState<iApc[]>([])

  const dataApp = window.localStorage.getItem('quotation');
  const adminRS = window.localStorage.getItem("Root")

  const Quotation = JSON.parse(dataApp || '')

  let aRecibir=Quotation.loan.amount ||  0
  const ruta=Quotation.loan.bank || '100'
  // const mortage=Quotation.borrower.monthlyResidenceFee

  const jobSector=Quotation.borrower.jobSector || ''
  const profession=Number(Quotation.borrower.profession || undefined)

  const salario=(Quotation.borrower.wage || 0) + ( Quotation.borrower.alloance || 0) + (Quotation.borrower.perDiem || 0)

  // 1	Casa Propia
  // 2	Padres o Familiares
  // 3	Casa Hipotecada
  // 4	Casa Alquilada

  let hipoteca_sn = Quotation.borrower.residenceType === "3" || false

  const idSectors = sectors.filter(st => st.sector === jobSector)
  const idSector = idSectors[0].id_sector
  const tmp_entity = laboralEntitys.filter(r => r.ruta === ruta && r.id_sector === idSector && r.id_profesion === profession)
  const discount_capacity = tmp_entity[0].discount_capacity
  const discount_capacity_hip = tmp_entity[0].discount_capacity_mortgage
  const debt_capacity_hip = tmp_entity[0].debt_capacity_mortgage
  const debt_capacity = tmp_entity[0].debt_capacity
  const pje_dscto = discount_capacity
  const pje_dscto_hip = discount_capacity_hip

  let ParamCalcLoan: IParamsCalcLoan[] = []
  const tmpParams = window.localStorage.getItem('params')
  if(tmpParams) {
    ParamCalcLoan = JSON.parse(tmpParams)
  }

  //***************************************/
  const pjeEndTC = 55
  const pjeEndAuto = 60
  const pjeCapPagoAuto = 35
  const letraMinAuto = 150
  //***************************************/
  //***************************************/

  const montoLoan = aRecibir
  refApc.sort(function (a, b) {
    if (Number(a.Saldo_Actual) < Number(b.Saldo_Actual)) {
      return 1;
    }
    if (Number(a.Saldo_Actual) > Number(b.Saldo_Actual)) {
      return -1;
    }
    // a must be equal to b
    return 0;
  });

  let fp0: iApc[] = [
    {
      "status": false,
      "message": "",

      "Agente_Economico": "GLOBAL BANK CORPORATION",
      "Dias_Atraso": 49,
      "Estado": "ACTULIZADA",
      "Fec_Actualiazacion": "2/23/2022",
      "Fec_Ini_Relacion": "8/24/2016",
      "Fec_Prescripcion": "",
      "Fec_Ultimo_pago": "2/21/2022",
      "Fec_Vencimiento": "3/5/2027",
      "Forma_Pago": "DESCUENTO DIRECTO",
      "Historial": "111111111",
      "Letra": 312.12,
      "Monto_Original": 75525,
      "Monto_Utimo_Pago": 312.12,
      "Num_Pagos": 115,
      "Observacion": "CUENTA CONTRA RESERVA",
      "Referencia": "2016810116",
      "Relacion": "HIPOTECA",
      "Saldo_Actual": 74216.20,

      "score": "550",
      "pi": "0",
      "exclusion": "0",
      "montoAplicado": 0,
      "montoARecibir": 0,
    },
    {
      "status": false,
      "message": "",

      "Agente_Economico": "BANISI, S.A.",
      "Dias_Atraso": 49,
      "Estado": "ACTULIZADA",
      "Fec_Actualiazacion": "2/23/2022",
      "Fec_Ini_Relacion": "8/24/2016",
      "Fec_Prescripcion": "",
      "Fec_Ultimo_pago": "2/21/2022",
      "Fec_Vencimiento": "3/5/2027",
      "Forma_Pago": "DESCUENTO DIRECTO",
      "Historial": "111111111",
      "Letra": 238.28,
      "Monto_Original": 20005.7,
      "Monto_Utimo_Pago": 238.28,
      "Num_Pagos": 115,
      "Observacion": "CUENTA CONTRA RESERVA",
      "Referencia": "2016810116",
      "Relacion": "PREST. PERSONAL",
      "Saldo_Actual": 20005.7,
      
      "score": "550",
      "pi": "0",
      "exclusion": "0",
      "montoAplicado": 0,
      "montoARecibir": 0,
    },
  ]

  if(adminRS !== 'admin') {
    fp0 = refApc.filter(f => f.Relacion !== "SERVICIOS")
  }
  for(let deuda of fp0) {
    if(deuda.Relacion === "L. ROTATIVA" || deuda.Relacion === "TMA"){
      deuda.Letra = (Number(deuda.Saldo_Actual) * 0.05)
    }
  
    if(deuda.Relacion === "TARJ. CREDITO"){
      deuda.Letra = (Number(deuda.Saldo_Actual) * 0.03)
    }

    deuda.montoAplicado = 0.00
    deuda.montoARecibir = 0.00
  }

  let score = 0
  if(fp0.length > 0) score = Number(fp0[0].score || 0)

  let saldoDD = 0, sumaLetraMesDD = 0, sumaLetraHip = 0, letrasP = 0, letrasT = 0
  for(let ref of fp0) {
    const { Saldo_Actual, Relacion, Letra=0, Forma_Pago } = ref
    let saldoActual = Number(Saldo_Actual)
    if (Relacion === 'HIPOTECA') {
      sumaLetraHip += Letra
      hipoteca_sn = true
    } else if(Forma_Pago === "DESCUENTO DIRECTO") {
      aRecibir -= saldoActual
      if(aRecibir >= 0) {
        ref.montoAplicado = saldoActual
        ref.montoARecibir = aRecibir
        letrasP += Number(Letra)
      }
      saldoDD += saldoActual
      sumaLetraMesDD += Number(Letra)
    } 
    letrasT += Number(Letra)
  }
  let fp = fp0; 
  if(!loansActive.length) {
    setLoansActive(fp)
  }


  const handleOnClick = () => {

    let contom = 1  // Por el momento se dejan pasar.  RS 2021-10-25
    // let contom = -1
    // for(let ref of fp0) {
    //   const { historia } = ref
    //   if(historia === undefined) {
    //     break
    //   }
      
    //   for(let l of historia) {
    //     if (l > "3") {
    //       contom = 0
    //       break
    //     }
    //   }
    //   if (contom === 0) break
    //   contom = 1
    // }  

    // fp = fp0.filter(r => r.Saldo_Actual !== r.montoAplicado); 
    // setLoansActive(fp)

    let capDescto = salario * pje_dscto/100
    let capDesctoHip = salario * pje_dscto_hip/100
    capDescto = Math.round(capDescto*100)/100
    capDesctoHip = Math.round(capDesctoHip*100)/100

    let newDispo = capDescto - sumaLetraMesDD
    let newDispoHip = capDesctoHip - (sumaLetraHip + sumaLetraMesDD)
    if(newDispoHip < newDispo) newDispo = newDispoHip
    
    if(newDispo+sumaLetraMesDD > newDispoHip) {
      newDispo = newDispoHip - sumaLetraMesDD
    }
    newDispo = Math.round(newDispo*100)/100

    let pjeCapEndDespPrest = (letrasT - letrasP + capDescto)/salario*100
    if(letrasP <= 0) {
      pjeCapEndDespPrest = letrasT/salario*100 
    }
    const pjeCapEndAntePrest = letrasT/salario*100 
    let endeudamiento = pjeCapEndDespPrest > (hipoteca_sn ? pje_dscto_hip : pje_dscto) ? 0 : 1

    let saldoPorDD = saldoDD > montoLoan ? 0 : 1


    let loanXtra = false
    if(!saldoPorDD && contom && endeudamiento && score >= 57 && newDispo >= 50) {
      saldoPorDD = 1
      loanXtra = true
      endeudamiento = (newDispo/salario*100) > (hipoteca_sn ? debt_capacity_hip : debt_capacity) ? 0 : 1

      let loan = 0
      const cap_discto_available = newDispo
      const tmp = ParamCalcLoan.filter(r => r.entidad === '100')
      const { wPlazo, comision, factor, segVida, factor_SV, itbms, servicioDescto, timbres, gastoLegal,letraRetenida } = tmp[0]

      if(ruta === '600') { //** FORMULA ESPECIAL FINANCOMER **/
        const tmp = ParamCalcLoan.filter(r => r.entidad === '600')
        const { wPlazo, plazoCalc, tasa, feci,Notaria } = tmp[0]
        loan = ((wPlazo*cap_discto_available)-Notaria)/(1+(plazoCalc*feci) + (0.00168*plazoCalc) + (tasa*plazoCalc) + 0.2675)
      } else if(ruta === '700') { //** FORMULA ESPECIAL PANACREDIT **/
        const tmp = ParamCalcLoan.filter(r => r.entidad === '700')
        const { wPlazo, comision, plazoCalc, tasa, feci, factor } = tmp[0]
        loan = ((cap_discto_available*wPlazo)-587.8)/(1.04815+comision+(0.083333*plazoCalc)*(tasa+feci)+factor*plazoCalc*0.00105)
      } else { // AQUI LOGICA DE RECALCULO STANDARD
        loan = (factor*cap_discto_available)/(1.00+(factor*segVida*factor_SV))
      }
      loan = Math.round(loan*100)/100
      loan = Number(loan.toFixed(2))
      const monthlyFee = cap_discto_available //Math.round(reFactor*loan*100)/100
      const comisClose = Math.round(comision*loan*100)/100
      const comisItbm = Math.round(comisClose*itbms*100)/100
      const servDescto = Math.round(cap_discto_available*wPlazo*servicioDescto*100)/100
      const comisTimbres = Math.round(loan*timbres*10)/10
      const comisGastoLegal = gastoLegal + cap_discto_available
      const montoLetRet = letraRetenida ? monthlyFee : 0
  
      aRecibir = loan - (comisClose+comisItbm+comisGastoLegal+servDescto+montoLetRet+comisTimbres)
      if(ruta === '600' || ruta === '700') {
        aRecibir = loan
      }
      if(aRecibir > 0) {
        Quotation.loan.amount = loan
        Quotation.loan.cashOnHand = aRecibir
        Quotation.loan.monthlyFee = newDispo
    
        window.localStorage.setItem('quotation', JSON.stringify(Quotation))

        fp.push(
          {
            "status": false,
            "message": "",
      
            "Agente_Economico": "** OBLILGACION ADICIONAL",
            "Dias_Atraso": 0,
            "Estado": "ACTULIZADA",
            "Fec_Actualiazacion": "2/23/2022",
            "Fec_Ini_Relacion": "8/24/2016",
            "Fec_Prescripcion": "",
            "Fec_Ultimo_pago": "2/21/2022",
            "Fec_Vencimiento": "3/5/2027",
            "Forma_Pago": "DESCUENTO DIRECTO",
            "Historial": "111111111",
            "Letra": newDispo,
            "Monto_Original": loan,
            "Monto_Utimo_Pago": 0,
            "Num_Pagos": 115,
            "Observacion": "",
            "Referencia": "999",
            "Relacion": "PREST. PERSONAL",
            "Saldo_Actual": loan,
      
            "score": "550",
            "pi": "0",
            "exclusion": "0",
            "montoAplicado": 0,
            "montoARecibir": loan,
          }
        )
        setLoansActive(fp)
      } else aRecibir = 0
    }
    const aplicaPP = contom + endeudamiento + saldoPorDD

    if(!loanXtra && contom && endeudamiento && score >= 57) {
      fp = fp0.filter(r => r.Saldo_Actual !== r.montoAplicado); 
      fp.push(
        {
          "status": false,
          "message": "",
    
          "Agente_Economico": "** OBLIGACION REEMPLAZO",
          "Dias_Atraso": 0,
          "Estado": "ACTULIZADA",
          "Fec_Actualiazacion": "2/23/2022",
          "Fec_Ini_Relacion": "8/24/2016",
          "Fec_Prescripcion": "",
          "Fec_Ultimo_pago": "2/21/2022",
          "Fec_Vencimiento": "3/5/2027",
          "Forma_Pago": "DESCUENTO DIRECTO",
          "Historial": "111111111",
          "Letra": capDescto,
          "Monto_Original": montoLoan,
          "Monto_Utimo_Pago": 0,
          "Num_Pagos": 115,
          "Observacion": "",
          "Referencia": "999",
          "Relacion": "PREST. PERSONAL",
          "Saldo_Actual": montoLoan,
    
          "score": "550",
          "pi": "0",
          "exclusion": "0",
          "montoAplicado": 0,
          "montoARecibir": aRecibir,
        }
      )
      setLoansActive(fp)
    }

    let aplicaAuto = 0, aplicaTC = 0, aplicaHip = 0, capEndAoDAuto = 0, capEndAoDTC = 0

    // Calculo capacidad de Endeuda. disponible para prestamo de auto
    if(pjeCapEndDespPrest < pjeEndAuto) capEndAoDAuto = pjeEndAuto - pjeCapEndDespPrest
    else if(pjeCapEndAntePrest < pjeEndAuto) capEndAoDAuto = pjeEndAuto - pjeCapEndAntePrest

    let capEndDispAuto = capEndAoDAuto*salario/100
    if(capEndDispAuto > pjeCapPagoAuto*salario/100) capEndDispAuto = pjeCapPagoAuto*salario/100
    if(capEndDispAuto >= letraMinAuto) aplicaAuto = 1
    aplicaAuto = 1

    // Calculo capacidad de Endeuda. disponible para TC
    if(pjeCapEndDespPrest < pjeEndTC) capEndAoDTC = pjeEndTC - pjeCapEndDespPrest
    else if(pjeCapEndAntePrest < pjeEndTC) capEndAoDTC = pjeEndTC - pjeCapEndAntePrest

    let capEndDispTC = capEndAoDTC*salario/100
    // if(score >= 57 && capEndDispTC > 0) aplicaTC = 1
    if(capEndDispTC > 0) aplicaTC = 1

    const prestAuto = calcMontoAuto()

    function calcMontoAuto() {
      const fCotiza = new Date()
      // let anioHoy = fCotiza.getFullYear(), mesHoy = fCotiza.getMonth()+1 , diaHoy = fCotiza.getDate()
      // for(let i=1; i<85; i++){
      //   if(mesHoy === 12) {
      //     mesHoy = 0
      //     anioHoy++
      //   }
      //   mesHoy++
      // }
      // mesHoy--

      let fVencto = new Date() // anioHoy, mesHoy, diaHoy)
      const daysLoan = 0 // ((fVencto.getTime() - fCotiza.getTime())/(24*60*60*1000)) + 1
      const promDiasxMes = 0 // daysLoan/plazo
      const pagos_x_anio = 0 // 360/promDiasxMes
      const tasaMes = 0 // (tasa+1)/pagos_x_anio/100

      const valorAuto = 0.00 //Math.max(Math.round(((capEndDispAuto/tasaMes*(1-(1/(1+tasaMes)**(plazo)))-total_gastos)/1-0.20)*100)/100,0)

      return {
        fCotiza,
        fVencto,
        daysLoan, 
        promDiasxMes, 
        pagos_x_anio, 
        tasaMes, 
        valorAuto
      }
    }

    setApplication({
      borrower: {
        contometro: contom, 
        endeudamiento,
        saldoPorDD,
        pjeCapEndeud: pjeCapEndDespPrest,
        saldoDD,
        aplicaPP,
        aplicaAuto,
        aplicaTC,
        aplicaHip,
        prestAuto: prestAuto.valorAuto,
        prestTC: 0,
        prestHip: 0,
        scoreAPC: score,
      }
    })

    const xx = document.getElementById('aplicar');
    if (xx)
      xx.setAttribute('style', 'display: none')
  }

  const styleButton = {
    padding: '10px 15px 10px 15px',
    border: 'none',
    borderRadius: '25px',
    background: '#0C7BDD',
    color: 'white'
  }

  const handleChange1 = (event: any) => {
    setApplication({
      borrower: {
        aplicaTCOk: event.target.checked
      }
    })
  }
  const handleChange2 = (event: any) => {
    setApplication({
      borrower: {
        aplicaAutoOk: event.target.checked
      }
    })
  }
  const handleChange3 = (event: any) => {
    setApplication({
      borrower: {
        aplicaHipOk: event.target.checked
      }
    })
  }

  const otrosProductos = () => {
    return (
      <>
        {!borrower.contometro ? (
          <>
          <br />
          <Typography variant="h6">Otras opciones a las que podria Precalificar</Typography>

            <Table className={classes.table} size="small" aria-label="a dense table" style={{border: '3px solid gray', width: '50%'}}>
              <TableHead>
                <TableRow>
                  <TableCell align="center" style={{fontSize: '20px'}}>Límite/Monto Máx.</TableCell>
                  <TableCell align="center" style={{borderLeft: '3px solid gray', fontSize: '20px'}}>Opciones</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {(borrower.aplicaTC || 0) === 1 ? (
                  <TableRow>
                    <TableCell>
                      <IconButton color="primary" className={classes.margin}>
                        <CreditCardIcon fontSize="large"/>
                        <Typography>&nbsp;&nbsp;{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(Number(borrower.prestTC))}</Typography>
                      </IconButton>
                    </TableCell>
                    <TableCell align='center' style={{borderLeft: '3px solid gray'}} component="th" scope="row">
                    <Checkbox
                      checked={borrower.aplicaTCOk}
                      onChange={handleChange1}                    
                      color="default"
                      name="aplicaTC"
                      inputProps={{ 'aria-label': 'checkbox with default color' }}
                    />
                    </TableCell>
                  </TableRow>
                ): null }
                {(borrower.aplicaAuto || 0) === 1 ? (
                  <TableRow>
                    <TableCell>
                      <IconButton color="primary" className={classes.margin}>
                        <DriveEtaIcon fontSize="large"/>
                        <Typography>&nbsp;&nbsp;{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(Number(borrower.prestAuto))}</Typography>
                      </IconButton>
                    </TableCell>
                    <TableCell align='center' style={{borderLeft: '3px solid gray'}} component="th" scope="row">
                    <Checkbox
                      checked={borrower.aplicaAutoOk}
                      onChange={handleChange2}
                      color="default"
                      name="aplicaAuto"
                      inputProps={{ 'aria-label': 'checkbox with default color' }}
                    />
                    </TableCell>
                  </TableRow>
                  ): null }
                  {(borrower.aplicaHip || 0) === 1 ? (
                  <TableRow>
                    <TableCell>
                      <IconButton color="primary" className={classes.margin}>
                        <HouseIcon fontSize="large"/>
                        <Typography>&nbsp;&nbsp;{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(Number(borrower.prestHip))}</Typography>
                      </IconButton>
                    </TableCell>
                    <TableCell align='center' style={{borderLeft: '3px solid gray'}} component="th" scope="row">
                    <Checkbox
                      checked={borrower.aplicaHipOk}
                      onChange={handleChange3}                    
                      color="default"
                      name="aplicaHip"
                      inputProps={{ 'aria-label': 'checkbox with default color' }}
                    />
                    </TableCell>                     
                  </TableRow>
                  ): null }
              </TableBody>
            </Table>
          </>
        ): null }
      </>
    )
  }

  return (
    <>
      <Typography variant="h4" align="center">Monto Solicitado: {new Intl.NumberFormat("en-US", {style: 'currency', currency: 'USD', minimumFractionDigits: 2}).format(montoLoan)}</Typography>
      <Typography color={"primary"} variant="h6">Obligaciones Activas Actuales</Typography>
      
      <AuthApcPdf {...props} /> 
      <ReferApcPdf {...props} />

      <TableContainer component={Paper}>
        <Table className={classes.table} size="small" aria-label="a dense table" style={{border: '3px solid gray'}}>
          <TableHead>
            <TableRow>
              <TableCell>Institución</TableCell>
              <TableCell>Obligación</TableCell>
              <TableCell>F. Pago</TableCell>
              <TableCell align="right">Saldo</TableCell>
              <TableCell align="right">Letra</TableCell>
              <TableCell align="right">A Recibir</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {loansActive.map((row) => (
              <TableRow key={row.Referencia}>
                <TableCell component="th" scope="row">
                  <Typography align={row.Referencia==='999' ? 'center' : 'left'} color={row.Referencia==='999' ? 'primary' : 'initial'}>{row.Agente_Economico === undefined ?row.message: row.Agente_Economico}</Typography>
                </TableCell>
                <TableCell>{row.Relacion}</TableCell>
                <TableCell>{row.Forma_Pago}</TableCell>
                <TableCell align="right">{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(Number(row.Saldo_Actual === undefined ? 0: row.Saldo_Actual))}</TableCell>
                <TableCell align="right">{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(Number(row.Letra === undefined ? 0: row.Letra))}</TableCell>
                <TableCell align="right">
                  <Typography color={row.Referencia==='999' ? 'primary' : 'initial'}>
                    {row.Referencia !== '999' ? '' : (new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(Number(row.montoARecibir === undefined ? 0: row.montoARecibir)))}
                  </Typography>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <div id="aplicar">
      <br />
      <button style={styleButton} onClick={handleOnClick}>Aplicar pago Obligaciones</button>
      <br />
      </div>
      {borrower.contometro !== undefined ? (
        <>
        { console.log('borrower.aplicaPP', borrower.aplicaPP) }
          {(borrower.aplicaPP !== 3) ? (
            <>
              <br />
              <Typography variant="h6">Lamentablemente usted no califica debido a los siguientes criterios</Typography>

              <TableContainer>
                  <Table className={classes.table} size="small" aria-label="a dense table" style={{border: '3px solid gray', width: '80%'}}>
                    <TableHead>
                      <TableRow>
                        <TableCell align="center" style={{fontSize: '20px'}}>Criterio</TableCell>
                        <TableCell align="center" style={{borderLeft: '3px solid gray', fontSize: '20px'}}>Comentario</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {!borrower.contometro ? (
                        <TableRow>
                          <TableCell>
                            <IconButton color="secondary" className={classes.margin}>
                              <CalendarTodayIcon fontSize="large"/>
                              <Typography>&nbsp;&nbsp;Días de Atraso</Typography>
                            </IconButton>
                          </TableCell>
                          <TableCell style={{borderLeft: '3px solid gray'}} component="th" scope="row">En 24 meses a mantenido atrasos &gt; 60 días</TableCell>
                        </TableRow>
                      ): null }
                      {!borrower.saldoPorDD ? (
                        <TableRow>
                          <TableCell>
                            <IconButton color="secondary" className={classes.margin}>
                              <EventBusyIcon fontSize="large"/>
                              <Typography>&nbsp;&nbsp;Nivel Endeudamiento</Typography>
                            </IconButton>
                          </TableCell>
                          <TableCell style={{borderLeft: '3px solid gray'}} component="th" scope="row">45% ( se excede de lo requerido )</TableCell>
                        </TableRow>
                        ): null }
                        {!borrower.endeudamiento ? (
                        <TableRow>
                          <TableCell>
                            <IconButton color="secondary" className={classes.margin}>
                              <LocalAtmIcon fontSize="large"/>
                              <Typography>&nbsp;&nbsp;Total Obligaciones por Descuento</Typography>
                            </IconButton>
                          </TableCell>
                          <TableCell style={{borderLeft: '3px solid gray'}} component="th" scope="row">Se excede de lo solicitado</TableCell>                     
                        </TableRow>
                        ): null }
                        {(borrower.scoreAPC || 0) < 57 ? (
                        <TableRow>
                          <TableCell>
                            <IconButton color="secondary" className={classes.margin}>
                              <SportsHandballIcon fontSize="large"/>
                              <Typography>&nbsp;&nbsp;Score APC</Typography>
                            </IconButton>
                          </TableCell>
                          <TableCell style={{borderLeft: '3px solid gray'}} component="th" scope="row">{borrower.scoreAPC}%</TableCell>
                        </TableRow>
                        ): null }
                    </TableBody>
                  </Table>
                </TableContainer>   
                {otrosProductos()}
            </>
          ): null }
        </>
      )
      : null }
    </>
  );
}

export default Form;