import React from 'react'
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Card, CardContent, Typography, Input, IconButton } from '@material-ui/core'
import useMediaQuery from '@material-ui/core/useMediaQuery';
import PublishIcon from '@material-ui/icons/Publish';
import Grid from '@material-ui/core/Grid';
import CameraAltOutlinedIcon from '@material-ui/icons/CameraAltOutlined';
import CardMedia from '@material-ui/core/CardMedia';
import CardActionArea from '@material-ui/core/CardActionArea';


const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    height: '100%',
    padding: '24',
    textAlign: "center"
  },
  button: {
    width: 50,
    height: 50,
    color: "#0C7BDD",
    align: "center"
  },
  texto: {
    fontSize: "0.8rem"
  },
  media: {
    height: 140,
  },
}));

const API_HOST = ''; //process.env.REACT_APP_API_HOST;

function CardDocument(props) {
  const theme = useTheme();
  const desktop = useMediaQuery(theme.breakpoints.up('sm'));
  const classes = useStyles();
  const { option, propsA } = props;
  const { application, application: { borrower, documents }, setApplication } = propsA;

  let typeDoc = ''
  let bank = ''
  let propsQ = ''
  const data = window.localStorage.getItem('quotation');
  if (data) {
    propsQ = JSON.parse(data)
    bank = propsQ.loan.bank
  }

  const takeFile = async (e) => {
    const { id } = borrower;

    if (e.target.files.length > 0) {
      const fileUpload = e.target.files[0]
      // console.log(e.target)
      typeDoc = e.target.id

      const  formData = new FormData()
      formData.set("idUrl", fileUpload)
      formData.set("entity_f", bank)
      formData.set("prospect", id)
      formData.set("nameImage", typeDoc)

      debugger
      // console.log('7777 ',fileUpload.name, bank, id, typeDoc, typeof formData, formData)
      try {
        const uploadFileRes = await fetch(`${API_HOST}/upload/file`, {
            method: 'POST',
            body: formData
        })

        const uploadFileData = await uploadFileRes.json()
        console.log(uploadFileData.Location)
        updateDocuments(uploadFileData.Location)
      } catch (e) {
        console.error(e);
      }
    }
  }

  function updateDocuments(location) {

    switch (typeDoc) {
      case 'CEDULA':
        documents.idUrl = location
      break;
      case 'COMP-PAGO':
        documents.payStubUrl = location
      break;
      case 'FICHA-SS':
        documents.socialSecurityProofUrl = location
      break;
      case 'SERV-PUBLICO':
        documents.publicGoodProofUrl = location
      break;
      case 'CARTA-TRABAJO':
        documents.workLetterUrl = location
      break;
      default:

    }

    setApplication({ documents: documents })
    window.localStorage.setItem('application', JSON.stringify(application));
  }

  return (
    <Grid item xs={6} sm={6} md={2}>
      {option.imgUrl ? (
        <Card className={classes.root}>
          <>
            <CardMedia
              className={classes.media}
              image={option.imgUrl}
              title={option.id_name}
            />
            <Typography className={classes.texto}>{option.name.toUpperCase()}</Typography>
          </>
          <CardActionArea style={{textAlign: 'center'}}>
            <IconButton variant="raised" component="label">
              {desktop ? <PublishIcon className={classes.button}/> : <CameraAltOutlinedIcon  className={classes.button}/>}
              <Input id={option.id_name} type="file" style={{display: 'none'}} accept="image/png, image/jpeg, application/pdf" onChange={takeFile}/>
            </IconButton>
          </CardActionArea>
        </Card>
      ) : (
        <Card className={classes.root}>
          <CardContent className={classes.root}>
            <label htmlFor="raised-button-file">
              <IconButton variant="raised" component="label">
                {desktop ? <PublishIcon className={classes.button}/> : <CameraAltOutlinedIcon  className={classes.button}/>}
                <Input id={option.id_name} type="file" style={{display: 'none'}} accept="image/png, image/jpeg, application/pdf" onChange={takeFile}/>
              </IconButton>
            </label> 
            <Typography className={classes.texto}>{option.name.toUpperCase()}</Typography>
          </CardContent>
        </Card>
      )}
    </Grid>
  )
}

export default CardDocument;