import React, { useState } from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles, Theme } from '@material-ui/core/styles';
import {  Modal, Button } from '@material-ui/core';
import Rechazo from '../../components/Images/images/img-rechazo.jpg'
import TextFieldF2 from '../Fields/TextFieldF2';
import { TextFieldF2e } from '../Fields/TextFieldF2';

const useStyles = makeStyles((theme: Theme) => ({
    bottomSpacing: {
      marginBottom: 40 //MRG_BOTT.bottomSpacing,
    },
    terminos: {
      marginBottom: 40,
      marginTop: 40,
      fontSize: 20
    },
    modal: {
      background: "white",
      border: "2px solid #000",
      boxShadow: theme.shadows[5],
      padding: "16px 30px 5px",
      transform: "tranlate(-50%, -50%)",
      height: '90%',
    
      [theme.breakpoints.down('lg')]: {
        height: '600px',
        width: '30%',
        cursor: 'pointer',
        margin: '55px auto'
      },
      [theme.breakpoints.down('xs')]: {
        height: '550px',
        cursor: 'pointer',
        position:'absolute',
        top:'2%',
        left:'5%',
        width: '90%',
      }
    },
    content: {
        padding: 12,
      
    },
    label: {
        marginTop: 10,
        marginBottom: 0,
        
    },
    text: {
        textAlign: 'center'
    }
}));


function LandingPage(props: any) {
  
    const styles = useStyles()
    const [modal, setModal] = useState(true)
    const [emailValid, setEmailValid] = useState(true)

    const { onChangeContacto, contacto, onGuardar } = props
    
    const abrircerrarmodal = (origen: number = 0) => {
        const emailRegex = /^[-\w.%+]{1,64}@(?:[A-Z0-9-]{1,63}\.){1,125}[A-Z]{2,63}$/i;
        //Se muestra un texto a modo de ejemplo, luego va a ser un icono
        if(origen === 2) {
            onGuardar(true)
            setModal(!modal)
        }
        if (emailRegex.test(contacto.email)) {
            onGuardar(true)
            setModal(!modal)
        } else {
            setEmailValid(false)
        }
    }
    
    function onApplicationChange(key: string, value: string) {
        onChangeContacto(key, value)  
    }

    const validaCampos = () => {
        const { name, email } = contacto
        if(name.length && email.length) {
            return false
        }
        return true
    }
    
    const body = (
        <Grid item xs={12} className={styles.modal}>
            <div className={styles.content}>
            </div>

            <div className={styles.text}>
                <img src={Rechazo} alt="" width="200"/>
            </div>
 
            <Grid container spacing={2}>
                <Grid item xs={12} classes={{item: styles.label}}>
                    <TextFieldF2
                        label="Nombre:"
                        onChange={(value: string) => onApplicationChange('name', value)}
                        error={!contacto.name}
                        />
                </Grid>
                <Grid item xs={12} classes={{item: styles.label}}>
                    <TextFieldF2e
                        label="Email:"
                        onChange={(value: string) => onApplicationChange('email', value)}
                        error={!emailValid}
                        message={!emailValid ? "Ingrese un Email válido ...": ""}
                        />
                </Grid>
                <Grid item xs={12}classes={{item: styles.label}}>
                    <TextFieldF2
                        label="# Teléfono:"
                        onChange={(value: string) => onApplicationChange('cellphone', value)}
                        error={!contacto.cellphone}

                        />
                </Grid>

                <Grid item sm={12} classes={{item: styles.label}}>
                    <Button 
                        onClick={()=>abrircerrarmodal(1)}
                        variant="contained" 
                        disabled={validaCampos()}

                        style={{marginLeft: '15px', backgroundColor: '#0C7BDD', color: '#FFFFFF'}}
                    >Aceptar</Button>
                    <Button 
                        onClick={()=>abrircerrarmodal(2)}
                        variant="contained" 
                        // disabled={validaCampos()}
                        style={{marginLeft: '15px', backgroundColor: '#0C7BDD', color: '#FFFFFF'}}
                    >Cerrar</Button>
                </Grid>
            </Grid>
        </Grid>
    )
  
    return (
        <div>
            <Modal
                disableEscapeKeyDown
                open={modal}
                onClose={(event, reason) => {
                    if(reason !== 'backdropClick') {
                        abrircerrarmodal()
                    }
                }}
            >
                {body}
            </Modal>
        </div>
    )
  }
  

  export default LandingPage;