import React from 'react';
import { makeStyles, Theme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';

interface Props {
  hasScrolled: boolean;
  children: React.ReactChild;
  onClick?: (event: React.MouseEvent<HTMLElement>) => void;
  startIcon?: React.ReactElement;
  endIcon?: React.ReactElement;
};


const useStyles = makeStyles((theme: Theme) => ({
  root: {
    color: (props: Props) => props.hasScrolled ? 'inherit' : theme.palette.common.white,
    display:'flex',
    alignItems:'center',
    textShadow: (props: Props) => props.hasScrolled ? 'unset' : '0px 4px 10px rgba(0, 0, 0, 0.25)',
    cursor: 'pointer',
    '&:hover': {
      color: (props: Props) => props.hasScrolled ? 'inherit' : theme.palette.grey['300']
    }
  },
  startIcon: {
    marginRight: 8,
    display:'flex',
    alignItems:'center',
  },
  endIcon: {
    marginLeft: 4,
    display:'flex',
    alignItems:'center',
  }
}));

function NavbarButton(props: Props) {
  const classes = useStyles(props);
  return(
    <Typography variant='body2' color='textPrimary' classes={{root: classes.root}} onClick={props.onClick}>
      <span className={classes.startIcon}>
        {props.startIcon && props.startIcon}
      </span>
      {props.children}
      <span className={classes.endIcon}>
        {props.endIcon && props.endIcon}
      </span>
    </Typography>
  );
}

export default NavbarButton;