import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';
import Form from './Form';

export interface IProvince {
  id: number;
  name: string;
}

export interface IDistrict {
  id: number;
  idProv: number;
  name: string;
}

export interface ICounty {
  id: number;
  idProv: number;
  idDist: number;
  name: string;
}

function Address(props: Props) {

  const { 
    data: provincesResp, 
    loading: provincesLoading
  } = useFetch<IProvince[]>("/provinces", { method: "GET" },
    []
  );
  const { 
    data: districtsResp, 
    loading: districtsLoading
  } = useFetch<IDistrict[]>("/districts", { method: "GET" },
    []
  );
  const { 
    data: countiesResp, 
    loading: countiesLoading
  } = useFetch<ICounty[]>("/counties", { method: "GET" },
    []
  );

  const loading = provincesLoading || districtsLoading || countiesLoading; 

  if (loading || !provincesResp || !districtsResp || !countiesResp) { 
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return (
    <Form 
      {...props} 
      tprovs={provincesResp} 
      tdists={districtsResp} 
      tcounties={countiesResp} 
    />);
};

export default Address;