import React from 'react';
import Grid from '@material-ui/core/Grid';
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import STRINGS from '../../../../../strings';

import { Props as BorrowerProps } from '../index';
import { LaboralSector, Profession } from './index';
import { JOB_SECTORS } from '../../types';

interface AutocompleteOption {
  id: string;
  name: string;
};

interface Props extends BorrowerProps {
  laboralSectors: LaboralSector[];
  planillas: Profession[];
};

function RetiredForm(props: Props) {
  const { quotation: { borrower }, setQuotation, laboralSectors, planillas } = props;

  function onRetirementSelected(event: React.ChangeEvent<{}>, option: AutocompleteOption | null, reason: string) {
    if (option) {
      setQuotation({ borrower: { retirement: option.id, profession: "7", occupation: option.id } });
    }
  }

  const retired = laboralSectors.find((laboralSector) => laboralSector.sector === JOB_SECTORS.RETIRED);
  // const professions = retired ? retired.professions : [];
  const professions = retired ? planillas : [];
  
  const retirementOptions = professions.map((profession) => ({
    id: String(profession.id),
    name: profession.name
  }));

  const occupation = retirementOptions.find((option) => (
    option.id === borrower.retirement
  ));

  return (
    <Grid container spacing={2} justify='center'>
      <Grid item xs={12} sm={11}>
        <Grid item xs={12} sm={6}>
          <Autocomplete
            onChange={onRetirementSelected}
            options={retirementOptions}
            value={occupation}
            getOptionLabel={(option: AutocompleteOption) => option.name}
            getOptionSelected={(option: AutocompleteOption, value: AutocompleteOption) => option.id === value.id}
            renderInput={(params) => <TextField {...params} label={STRINGS.QUOTATION.PERSONAL_LOAN.OCCUPATION.CHOOSE_RETIREMENT} />}
          />
        </Grid>
      </Grid>
    </Grid>   
  );
};

export default RetiredForm;