import React from 'react';
import { useHistory } from "react-router-dom";
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Hidden from '@material-ui/core/Hidden';
import { makeStyles, Theme } from '@material-ui/core/styles';

import PRODUCTS from '../../lib/products';

import { PanamanianFlagIcon } from '../Svg';
import { FinanservesVerticalLogoImg } from '../Images';

import BrandLine from './BrandLine';

const useStyles = makeStyles((theme: Theme) => ({
  container: {
    paddingTop: 56,
    paddingBottom: 56
  },
  menu: {
    marginBottom: 56
  },
  logo: {
    height: 150
  },
  listItem: {
    cursor: 'pointer',
    '&:hover': {
      color: theme.palette.primary.dark
    }
  },
  listItemDisabled: {
    cursor: 'unset',
    '&:hover': {
      color: 'unset'
    }
  }
}));



function Footer() {
  const classes = useStyles();
  let history = useHistory();

  function onNavigationClick(elementId: string) {
    const target = document.querySelector(elementId);

    if (target) {
      target.scrollIntoView({ behavior: 'smooth', block: 'start' });
    } else {
      history.push(`//${elementId}`)
    }
  }

  function onTermsClick() {
    // history.push('/terminos-condiciones')
    history.push('/politica-de-privacidad')
  }


  function onProductClick(path: string) {
    history.push(path);
  }

  return (
    <>
      <Container classes={{ root: classes.container }}>
        <Grid container spacing={2} classes={{ root: classes.menu }}>
          <Grid item xs={6} sm={4} md={6}>
            <FinanservesVerticalLogoImg className={classes.logo} />
          </Grid>
          <Hidden xsDown>
            <Grid item sm={4} md={3}>
              <Typography variant='h6'>Compara</Typography>
              <List>
                {Object.values(PRODUCTS).map((product, idx) => (
                  <ListItem key={idx} disableGutters onClick={() => !product.disabled && onProductClick(product.path)} disabled={product.disabled}>
                    <ListItemText 
                      primary={product.name} 
                      primaryTypographyProps={{ variant: 'body2' }} 
                      classes={{ root: `${classes.listItem} ${product.disabled && classes.listItemDisabled}` }} 
                    />
                  </ListItem>
                ))}
              </List>
            </Grid>
          </Hidden>
          <Grid item xs={6} sm={4} md={3}>
            <Typography variant='h6'>Ayuda</Typography>
            <List>
              <ListItem disableGutters onClick={() => onNavigationClick('#steps')}>
                <ListItemText primary='¿Cómo empezar?' primaryTypographyProps={{ variant: 'body2' }} classes={{ root: classes.listItem }} />
              </ListItem>
              <ListItem disableGutters onClick={() => onNavigationClick('#faqs')}>
                <ListItemText primary='Preguntas frecuentes' primaryTypographyProps={{ variant: 'body2' }} classes={{ root: classes.listItem }} />
              </ListItem>
              <ListItem disableGutters onClick={() => onTermsClick()}>
                <ListItemText primary='Términos y condiciones' primaryTypographyProps={{ variant: 'body2' }} classes={{ root: classes.listItem }} />
              </ListItem>
            </List>
          </Grid>
        </Grid>
        <Grid container spacing={2} alignItems='flex-end'>
          <Grid item xs={10} sm={11}>
            <Typography color='textSecondary' variant='body2'>
              Copyright © 2020, ACsoraT, S. A. Todos los derechos reservados.
            </Typography>
          </Grid>
          <Grid item xs={2} sm={1}>
            <PanamanianFlagIcon fontSize='large' />
          </Grid>
        </Grid>
      </Container>
      <BrandLine />
    </>
  );
};

export default Footer;