import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';

import Form from './Form';

export interface LaboralStatus {
  name: string;
  is_active: boolean;
}

function Employment(props: Props) {
  const { data: laboralStatus, loading } = useFetch<LaboralStatus[]>(
    "/laboral_status",
    { method: "GET" },
    []
  );

  if (loading || !laboralStatus) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form {...props} laboralStatus={laboralStatus} />;
};

export default Employment;