import React from 'react';
import Grid from '@material-ui/core/Grid';
import { Props as ApplicationProps } from '../index';
import { Document } from './index';
import CardDocument from './CardDocument';
import PopUpFiles from '../../../../../components/PopUpFiles/index';

interface Props extends ApplicationProps {
  documentos: Document[]
}

function Form(props: Props) {
  const { application: { documents }, documentos } = props;

  let images = [{
    imgUrl: '',
    id_name: '',
    name: ''
  }]

  for(let doc of documentos) {
    if(doc.id_name === 'CEDULA') {
      images.push({
        imgUrl: documents.idUrl || '',
        id_name: doc.id_name,
        name: doc.name
      })
    }
    if(doc.id_name === 'FICHA-SS') {
      images.push({
        imgUrl: documents.socialSecurityProofUrl || '',
        id_name: doc.id_name,
        name: doc.name
      })
    }
    if(doc.id_name === 'SERV-PUBLICO') {
      images.push({
        imgUrl: documents.publicGoodProofUrl || '',
        id_name: doc.id_name,
        name: doc.name
      })
    }
    if(doc.id_name === 'COMP-PAGO') {
      images.push({
        imgUrl: documents.payStubUrl || '',
        id_name: doc.id_name,
        name: doc.name
      })
    }
    if(doc.id_name === 'CARTA-TRABAJO') {
      images.push({
        imgUrl: documents.workLetterUrl || '', 
        id_name: doc.id_name,
        name: doc.name
      })
    }
  }

  if(images.length > 0) images = images.slice(1,)

  return (
    <Grid container spacing={3}>
      {images.map((item, idx) => (
        <CardDocument key={idx} option={item} propsA={props} />
      ))}
      <PopUpFiles />
    </Grid>
  );
}

export default Form;