import React, { useState, useEffect } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import { makeStyles, Theme } from '@material-ui/core/styles';
import SentimentDissatisfiedOutlinedIcon from '@material-ui/icons/SentimentDissatisfiedOutlined';

import { ProductCard } from '../../../components/Cards';

import PRODUCTS from '../../../lib/products';
import STRINGS from '../../../strings';
import LandingPage from '../../../components/LandingPage/index';


const useStyles = makeStyles((theme: Theme) => ({
  container: {
    display: 'flex', 
    flexDirection: 'column', 
    height: '100%', 
    paddingTop: 72, 
    paddingBottom: 72,
    [theme.breakpoints.down('sm')]: {
      paddingTop: 24, 
      paddingBottom: 24
    }
  },
  title: {
    display: 'flex',
    alignItems: 'center',
    marginBottom: 72
  },
  content: {
    textAlign:'center',
    marginBottom: 80
  },
  icon: {
    color: theme.palette.secondary.main,
    height: 120,
    width: 120,
    marginBottom: 36
  }
}));


function Ineligible() {
  const classes = useStyles();
  let history = useHistory();

  const { search } = useLocation();
  const query = new URLSearchParams(search)
  const causas = query.get("causas") || ""
  const track = query.get("track")

  function onProductClick(path: string) {
    history.push(path);
  }

  const [contacto, setContacto] = useState({
    name: "",
    email: "",
    cellphone: ""
  })

  const [onGuardar, setonGuardar] = useState(false)

  function onChangeContacto(key: string, value: string) {
    setContacto({ 
        ...contacto,
        [key]: value
    })     
  }

  const tracking = async (causas: string) => {
    //***********************************************************//
    //***********************************************************//
    // Buscar Token de Clientify
    //***********************************************************//
    //***********************************************************//
    let Token = null
    let Clientify = window.localStorage.getItem('Clientify') || ''
    if (Clientify) {
      Token = JSON.parse(Clientify).token
    }

    if (!Token) {
      const response: Response = await fetch("/api/clientify-token", {
        method: 'POST'
      })

      // Guardamos Token de Clientify
      const data = await response.json();
      Token = data.token
      window.localStorage.setItem("Clientify",  JSON.stringify(data))
    }
    const IDClientify = window.localStorage.getItem('IDClientify') || ''

    // Guardamos la causa del Rechazo
    var raw = {
      "token": Token,
      "ID": IDClientify,
      "first_name": contacto.name,
      "email": contacto.email,
      "phone": contacto.cellphone,
      "Tracking": "RECHAZO POR: " + causas.toUpperCase(),
    };

    const response = await fetch("/api/clientify-rechazo", {
      method: "POST",
      body:  new URLSearchParams(raw)
    })

    const result = await response.json();
    // Guardamos id del contacto en Clientify
    window.localStorage.setItem("IDClientify",  JSON.stringify(result.id))
  }

  useEffect(() => {
    if(onGuardar) {
      tracking(causas)
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [onGuardar])

  return (
    <>
        <Container classes={{root: classes.container}}>
        <Typography classes={{root: classes.title}} variant='h5'>
          <PRODUCTS.PERSONAL_LOAN.icon color='primary' fontSize='large' style={{ marginRight: 16 }}/>
          {PRODUCTS.PERSONAL_LOAN.name}  
        </Typography>
        <div style={{flex:1, marginBottom: 56}}>
          <Grid container justify='center'>
            <Grid item xs={12} sm={6} classes={{item: classes.content}}>
              <SentimentDissatisfiedOutlinedIcon classes={{root: classes.icon}} />
              <Typography align='center' variant='h5' paragraph>{STRINGS.QUOTATION.PERSONAL_LOAN.INELIGIBLE.TITLE}</Typography>
              <Typography align='center' color='textSecondary'>{STRINGS.QUOTATION.PERSONAL_LOAN.INELIGIBLE.CONTENT}</Typography>
              <Typography align='center' color='textPrimary'>{causas}</Typography>
            </Grid>
          </Grid>
          <Typography align='center' paragraph>{STRINGS.QUOTATION.PERSONAL_LOAN.INELIGIBLE.PRODUCTS}</Typography>
          <Grid container spacing={2} justify='center'>
            {Object.values(PRODUCTS).map((product) => {
              if (product.name === STRINGS.PRODUCTS.PERSONAL_LOAN) {
                return null;
              }

              return (
                <Grid item xs={6} sm={4} md={4} lg={2}>
                  <ProductCard 
                    key={product.id}
                    disabled={product.disabled} 
                    Icon={product.icon}
                    title={product.name}
                    onClick={() => onProductClick(product.path)} 
                  />
                </Grid>
              );
            })}
          </Grid>
        </div>
        {track ?
        (
          <LandingPage 
            contacto={contacto}
            onChangeContacto={onChangeContacto}
            onGuardar={setonGuardar}
          />
        ) : "" }
      </Container>
      </>
  );
}

export default Ineligible;