import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';

import Form from './Form';

export interface ResidenceType {
  id: number;
  name: string;
  is_active: boolean;
}

function Residence(props: Props) {
  const { data: residenceTypes, loading } = useFetch<ResidenceType[]>(
    "/housing_types",
    { method: "GET" },
    []
  );

  if (loading || !residenceTypes) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form {...props} residenceTypes={residenceTypes} />;
};

export default Residence;